.account {
  padding: 50px 0;
  @include response(991px) {
    padding: 50px 20px;
  }
  @include response(767px) {
    padding: 0;
  }
  &__form {
    .form-block {
      margin-bottom: 30px;
    }
  }
  &__inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    max-width: 1190px;
    padding: 0 15px;
    width: 100%;
    margin: 0 auto;
    @include response(767px) {
      flex-direction: column-reverse;
    }
  }
  &__main {
    max-width: 730px;
    width: 100%;
    flex-basis: 730px;
    @include response(767px) {
      background: #fff;
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__box {
    width: 100%;
    padding: 50px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    @include response(991px) {
      padding: 50px 30px;
    }
    @include response(767px) {
      padding: 30px 20px 50px;
      border-radius: 0;
      border: none;
    }
  }
  &__aside {
    background: $black;
    max-width: 320px;
    width: 100%;
    flex-basis: 320px;
    margin-left: 20px;
    border-radius: 5px;
    flex-shrink: 0;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
      flex-shrink: 1;
      margin: 0 auto;
      border-radius: 0;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 27px;
    margin-bottom: 15px;
    @include response(767px) {
      font-size: 22px;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    margin-bottom: 24px;
  }
  &__form {
    .main-input {
      height: 46px;
      font-size: 16px;
      border: 1px solid #9C9EA1;
      @include response(767px) {
        height: 40px;
      }
    }
    .submit-button {
      max-width: 230px;
      margin: 50px auto 0;
      svg {
        margin: 0 10px 0 0;
      }
      @include response(767px) {
        margin: 44px 0 0;
      }
    }
  }
  &__head {
    padding: 25px 30px 15px;
  }
  &__head-bar {
    @extend %flex-v;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  &__head-text {
    font-size: 12px;
    color: #ebebee;
  }
  &__head-link {
    font-weight: 600;
    font-size: 12px;
    color: #ebebee;
    text-decoration: none;
    @include hover() {
      text-decoration: underline;
    }
  }
  &__profile {
    @extend %flex-v;
  }
  &__profile-avatar {
    @extend %flex-c;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: #5a5e62;
    overflow: hidden;
  }
  &__profile-info {
    margin-left: 15px;
  }
  &__profile-name {
    font-weight: 500;
    font-size: 14px;
    color: #ebebee;
  }
  &__profile-reviews {
    font-size: 12px;
    color: $brown;
    margin-top: 5px;
  }
  &__view {
    width: 100%;
    border-top: 1px solid rgba(89, 93, 97, 0.5);
    border-bottom: 1px solid rgba(89, 93, 97, 0.5);
    padding: 15px 30px;
    a {
      @extend %flex-v;
      font-size: 12px;
      color: #fff;
      text-decoration: none;
      svg {
        margin-left: 7px;
        transition: .3s ease-in-out;
      }
      @include hover() {
        svg {
          margin-left: 14px;
        }
      }
    }
  }
  &__nav {
    padding: 30px 30px 34px;
  }
  &__nav-list {
    @extend %list;
    li {
      margin-bottom: 10px;
      &:last-of-type {
        margin-bottom: 0;
      }
      a {
        @extend %flex-v;
        background: #5a5e62;
        border-radius: 5px;
        width: 100%;
        height: 46px;
        padding: 0 15px;
        font-size: 14px;
        color: #fff;
        text-decoration: none;
        transition: .3s ease-in-out;
        svg {
          margin-right: 10px;
        }
        @include hover() {
          background: $green;
        }
      }
      &.active {
        a {
          background: $green;
        }
      }
    }
  }
  &__lost-password {
    font-weight: 500;
    font-size: 14px;
    @extend %flex-v;
    justify-content: flex-end;
    margin: 20px 0 25px;
    @include response(767px) {
      justify-content: flex-start;
    }
    a {
      color: $green;
      text-decoration: none;
      @include hover() {
        text-decoration: underline;
      }
    }
  }
  &__delimiter {
    border-bottom: 1px solid #ebebee;
    margin: 30px -50px 30px;
    @include response(991px) {
      margin: 30px -30px 30px;
    }
  }
  &__notifications {
    margin: 28px 0 30px;
    @include response(767px) {
      flex-direction: column;
      align-items: flex-start;
      margin: 25px 0 -30px;
    }
  }
  &__notifications-content {
    width: 100%;
    margin-bottom: 5px;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
      margin: 0 0 15px;
    }
  }
  &__notifications-title {
    font-size: 16px;
    line-height: 175%;
  }
  &__notifications-email {
    font-weight: 600;
    font-size: 15px;
    color: $green;
    text-decoration: none;
    @include hover() {
      text-decoration: underline;
    }
  }
  &__notifications-button {
    display: inline-flex;
    align-items: center;
    color: #F31C45;
    text-decoration: none;
    span {
      text-decoration: underline;
    }
    svg {
      margin-left: 8px;
    }
  }
  .update-account {
    margin: 0;
    height: 46px;
    max-width: 330px;
    svg {
      margin-left: 10px;
    }
  }
  &__subscribe-form {
    .account__title {
      font-weight: 600;
      font-size: 24px;
      margin-bottom: 8px;
      @include response(767px) {
        font-weight: 500;
        font-size: 18px;
        margin-bottom: 10px;
      }
    }
    .account__text {
      font-weight: 500;
      font-size: 14px;
      margin-bottom: 30px;
      @include response(767px) {
        margin-bottom: 22px;
      }
    }
  }
  &__subscribe-row {
    @extend %flex-v;
    margin-bottom: 50px;
    .main-input {
      max-width: 310px;
      width: 100%;
      height: 55px;
      font-size: 16px;
      @include response(991px) {
        max-width: 100%;
      }
      @include response(767px) {
        height: 40px;
      }
    }
    .submit-button {
      max-width: 170px;
      width: 100%;
      margin-left: 10px;
      height: 55px;
      @include response(991px) {
        margin: 20px 0 0;
      }
      @include response(767px) {
        height: 48px;
      }
    }
    @include response(991px) {
      flex-direction: column;
      align-items: flex-start;
    }
    @include response(767px) {
      margin-bottom: 32px;
    }
  }
  &__subscribe-info {
    @extend %flex-v;
    font-weight: 500;
    font-size: 14px;
    svg {
      margin-left: 7px;
    }
  }
  &__about {
    padding: 35px 30px 30px;
  }
  &__about-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    color: #fff;
    margin-bottom: 15px;
  }
  &__about-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    color: #ebebee;
  }
  &__buttons {
    padding: 15px 30px 30px;
  }
  &__button {
    @extend %flex-c;
    width: 100%;
    height: 46px;
    border-radius: 5px;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    text-decoration: none;
    border: none;
    cursor: pointer;
    transition: .3s ease-in-out;
    svg {
      margin-left: 10px;
    }
    & + .account__button {
      margin-top: 10px;
    }
    &.gray {
      background: #5a5e62;
      @include hover() {
        background: rgba(90, 94, 98, 0.75);
      }
    }
    &.green {
      background: $green;
      @include hover() {
        background: rgba(27, 160, 152, 0.75);
      }
    }
  }
}

.account-success-msg {
  @extend %flex-v;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 10px;
  background: $green;
  border-radius: 5px;
  padding: 15px 20px 15px 15px;
  @include response(767px) {
    margin: 20px;
    width: auto;
    padding: 8px 15px;
  }
  &__text {
    @extend %flex-v;
    font-size: 16px;
    color: #fff;
    margin-right: 10px;
    svg {
      margin-right: 15px;
      @include response(767px) {
        margin-right: 10px;
      }
    }
  }
  &__close {
    cursor: pointer;
    svg {
      transition: .3s ease-in-out;
    }
    @include hover() {
      svg {
        transform: rotate(90deg);
      }
    }
  }
}

.profile {
  padding: 50px 0;
  @include response(767px) {
    padding: 0;
  }
  &__inner {
    max-width: 1190px;
    padding: 0 15px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include response(1200px) {
      padding: 0 20px;
    }
    @include response(767px) {
      flex-direction: column-reverse;
      padding: 0;
    }
  }
  &__main {
    max-width: 730px;
    width: 100%;
    flex-basis: 720px;
    min-width: 0;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
}

.profile-welcome {
  max-width: 100%;
  background: #fff;
  border: 1px solid #ebebee;
  border-radius: 5px;
  @include response(767px) {
    border: none;
  }
  &__head {
    padding: 50px 50px 26px;
    border-bottom: 1px solid #ebebee;
    @include response(991px) {
      padding: 30px 20px 20px;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    margin-bottom: 12px;  
    @include response(767px) {
      font-size: 22px;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
  }
  &__list {
    padding: 30px 50px;
    border-bottom: 1px solid #ebebee;
    @include response(991px) {
      padding: 20px;
    }
    ul {
      @extend %list;
      li {
        position: relative;
        padding: 0 0 0 23px;
        font-weight: 400;
        font-size: 15px;
        margin-bottom: 22px;
        &:last-of-type {
          margin-bottom: 0;
        }
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background: $green;
        }
      }
    }
  }
  &__list-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 15px;
    @include response(767px) {
      font-size: 18px;
    }
  }
  &__review {
    padding: 30px 50px 30px;
    border-bottom: 1px solid #ebebee;
    @include response(991px) {
      padding: 25px 20px 30px;
    }
  }
  &__review-title {
    @extend %flex-v;
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 15px;
    svg {
      margin-right: 10px;
    }
    @include response(767px) {
      font-size: 18px;
    }
  }
  &__review-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    margin-bottom: 24px;
  }
  &__review-subtitle {
    font-weight: 600;
    font-size: 12px;
    color: #5a5e62;
    margin-bottom: 10px;
  }
  &__review-controls {
    @extend %flex-v;
    justify-content: space-between;
  }
  &__review-link {
    @extend %flex-v;
    font-weight: 500;
    font-size: 14px;
    color: $green;
    text-decoration: none;
    svg {
      margin-left: 10px;
      transition: .3s ease-in-out;
    }
    @include hover() {
      svg {
        margin-left: 15px;
      }
    }
  }
  &__review-slider {
    margin-right: -47px;
    margin-bottom: 25px;
    @include response(991px) {
      margin-right: -17px;
    }
    .best-products__list {
      margin: 0 -3px;
    }
    .best-products__item {
      max-width: 260px;
      flex-basis: 260px;
      margin: 0 3px;
    }
  }
  &__review-navigation {
    @extend %flex-v;
    margin: 0 -5px;
    .slide-arrow {
      margin: 0 5px;
      cursor: pointer;
    }
  }
  &__contacts {
    padding: 30px 50px 50px;
    @include response(991px) {
      padding: 25px 20px 50px;
    }
  }
  &__contacts-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 15px;
    @include response(767px) {
      font-size: 18px;
    }
  }
  &__contacts-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    margin-bottom: 26px;
  }
  &__contacts-button {
    @extend %flex-c;
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: #fff;
    max-width: 140px;
    width: 100%;
    height: 46px;
    background: $black;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    text-decoration: none;
    transition: .3s ease-in-out;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: rgba(11, 16, 20, 0.75);
    }
  }
}

.edit-profile {
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
  background: $black;
  border-radius: 5px;
  @include response(991px) {
    border-radius: 0;
  }
  .main-textarea {
    background: #5a5e62;
    border-color: #5a5e62;
    color: #ebebee;
    resize: vertical;
    height: 200px;
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    &::placeholder {
      color: rgba(235, 235, 238, 0.6);
    }
    @include response(767px) {
      height: 230px;
    }
  }
  .main-input {
    border-color: #5a5e62;
    background: #5a5e62;
    border-radius: 5px;
    color: #ebebee;
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    height: 46px;
    &::placeholder {
      color: rgba(235, 235, 238, 0.6);
    }
  }
  &__head {
    padding: 50px 50px 22px;
    border-bottom: 1px solid rgba(90, 94, 98, 0.5);
    @include response(767px) {
      padding: 30px 20px;
    }
  }
  &__head-title {
    font-weight: 600;
    font-size: 27px;
    color: #fff;
    @include response(767px) {
      font-size: 22px;
    }
  }
  &__avatar {
    padding: 30px 50px;
    border-bottom: 1px solid rgba(90, 94, 98, 0.5);
    @include response(767px) {
      padding: 30px 20px;
    }
  }
  &__subtitle {
    font-weight: 500;
    font-size: 20px;
    color: #fff;
    margin-bottom: 15px;
    @include response(767px) {
      font-size: 18px;
    }
  }
  &__avatar-box {
    @extend %flex-v;
    @include response(767px) {
      align-items: flex-start;
    }
  }
  &__avatar-image {
    @extend %flex-c;
    width: 84px;
    height: 84px;
    border-radius: 50%;
    overflow: hidden;
    background: #5a5e62;
    margin-right: 25px;
    flex-shrink: 0;
    @include response(767px) {
      width: 64px;
      height: 64px;
    }
  }
  &__avatar-caption {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    color: #fff;
    margin-bottom: 8px;
    span {
      color: rgba(255, 255, 255, 0.6);
    }
    @include response(767px) {
      font-size: 14px;
    }
  }
  &__avatar-upload {
    label {
      @extend %flex-c;
      max-width: 112px;
      width: 100%;
      height: 46px;
      cursor: pointer;
      background: $green;
      border-radius: 5px;
      border: none;
      transition: .3s ease-in-out;
      input {
        display: none;
      }
      span {
        @extend %flex-c;
        font-weight: 500;
        font-size: 14px;
        color: #fff;
        svg {
          margin-left: 10px;
        }
      }
      @include hover() {
        background: rgba(27, 160, 152, 0.75);
      }
    }
  }
  &__about {
    padding: 30px 50px 15px;
    border-bottom: 1px solid rgba(90, 94, 98, 0.5);
    @include response(767px) {
      padding: 30px 20px 15px;
    }
  }
  &__socials {
    padding: 30px 50px 15px;
    border-bottom: 1px solid rgba(90, 94, 98, 0.5);
    .form-label {
      color: #fff;
    }
    @include response(767px) {
      padding: 30px 20px 15px;
    }
  }
  &__bottom {
    @extend %flex-v;
    justify-content: space-between;
    padding: 30px 50px;
    .submit-button {
      max-width: 160px;
    }
    @include response(767px) {
      padding: 30px 20px 50px;
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__bottom-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    color: #ebebee;
    margin-right: 20px;
    @include response(767px) {
      margin: 0 0 20px;
    }
  }
  &__success {
    width: 100%;
    @extend %flex-v;
    justify-content: space-between;
    padding: 15px 15px 15px 25px;
    border-radius: 5px;
    background: $green;
    margin-bottom: 85px;
    @include response(767px) {
      flex-direction: column;
      padding: 10px 15px;
      margin-bottom: 40px;
    }
  }
  &__success-msg {
    @extend %flex-v;
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    color: #fff;
    margin-right: 20px;
    svg {
      margin-right: 10px;
      flex-shrink: 0;
    }
    @include response(767px) {
      margin: 0 0 10px;
    }
  }
  &__success-button {
    @extend %flex-c;
    max-width: 164px;
    background: $black;
    svg {
      margin: 0 10px 0 0;
    }
    @include hover() {
      background: rgba(11, 16, 20, 0.75);
    }
  }
}

.public-profile {
  padding: 50px 0 100px;
  @include response(991px) {
    padding: 0;
  }
  &__inner {
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
    @include response(991px) {
      max-width: 100%;
    }
  }
  &__account {
    background: $black;
    border-radius: 5px;
    margin-bottom: 20px;
    @include response(991px) {
      border-radius: 0;
      margin-bottom: 0;
    }
  }
  &__account-head {
    padding: 50px 50px 16px;
    @include response(991px) {
      padding: 20px;
    }
  }
  &__account-row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 30px;
    @include response(767px) {
      margin-bottom: 24px;
    }
  }
  &__account-info {
    @extend %flex-v;
    @include response(767px) {
      align-items: flex-start;
    }
  }
  &__account-avatar {
    @extend %flex-c;
    flex-shrink: 0;
    width: 84px;
    height: 84px;
    background: #5a5e62;
    border-radius: 50%;
    margin-right: 25px;
    @include response(767px) {
      width: 64px;
      height: 64px;
      margin-right: 15px;
    }
  }
  &__account-name {
    @extend %flex-v;
    font-weight: 600;
    font-size: 18px;
    color: #ebebee;
    @include response(767px) {
      font-size: 16px;
      flex-wrap: wrap;
      span {
        width: 100%;
        display: block;
      }
    }
  }
  &__account-reviews {
    font-weight: 400;
    font-size: 16px;
    color: $brown;
    @include response(767px) {
      font-size: 14px;
      margin-bottom: 5px;
    }
  }
  &__account-about {
    font-size: 16px;
    line-height: 175%;
    color: #ebebee;
  }
  &__account-button {
    position: relative;
    margin-left: 10px;
    svg {
      cursor: pointer;
      &:hover {
        + .public-profile__account-tooltip {
          visibility: visible;
          opacity: 1;
          overflow: visible;
        }
      }
    }
    &:last-of-type {
      margin-left: 4px;
    }
    @include response(767px) {
      margin: 5px 0 0;
    }
  }
  &__account-tooltip {
    position: absolute;
    left: 0;
    top: 100%;
    margin-top: 6px;
    padding: 5px 8px;
    background: #5a5e62;
    border-radius: 2px;
    font-weight: 500;
    font-size: 12px;
    color: #fff;
    min-width: 146px;
    transition: .3s ease-in-out;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    z-index: 2;
    &::before {
      position: absolute;
      bottom: 100%;
      left: 8px;
      @include triangle(up, 5px, #5a5e62);
    }
  }
  &__account-flag {
    @extend %flex-v;
    font-weight: 500;
    font-size: 12px;
    color: rgba(235, 235, 238, 0.6);
    margin-right: 20px;
    svg {
      margin-right: 6px;
    }
  }
  &__account-options {
    @extend %flex-v;
  }
  &__account-settings {
    position: relative;
  }
  &__account-settings-button {
    position: relative;
    width: 36px;
    height: 36px;
    border: 2px solid #5a5e62;
    border-radius: 50%;
    cursor: pointer;
    transition: .3s ease-in-out;
    .line {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background: #fff;
      transition: .3s ease-in-out;
      &--1 {
        top: 6px;
      }
      &--2 {
        top: 50%;
        transform: translate(-50%, -50%);
      }
      &--3 {
        bottom: 6px;
      }
    }
    @include hover() {
      background: #5a5e62;
    }
    &.active {
      .line {
        border-radius: 1px;
        &--1 {
          height: 2px;
          width: 16px;
          transform: translateX(-50%) rotate(45deg);
          top: 16px;
        }
        &--2 {
          visibility: hidden;
          opacity: 0;
          overflow: hidden;
        }
        &--3 {
          height: 2px;
          width: 16px;
          transform: translateX(-50%) rotate(-45deg);
          bottom: 14px;
        }
      }
    }
  }
  &__account-settings-list {
    position: absolute;
    width: 100px;
    background: #fff;
    border-radius: 2px;
    right: 0;
    top: 100%;
    margin-top: 8px;
    z-index: 2;
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
    transition: .3s ease-in-out;
    &::before {
      position: absolute;
      bottom: 100%;
      right: 14px;
      @include triangle(up, 5px, #fff);
    }
    ul {
      @extend %list;
      li {
        a {
          text-align: right;
          width: 100%;
          display: block;
          background: #fff;
          font-size: 12px;
          color: $black;
          text-decoration: none;
          padding: 12px 15px;
          border-bottom: 1px solid #ebebee;
          transition: .3s ease-in-out;
          @include hover() {
            background: #ebebee;
          }
        }
        &:last-of-type {
          a {
            border: none;
          }
        }
      }
    }
    &.open {
      visibility: visible;
      opacity: 1;
      overflow: visible;
    }
  }
  &__account-bottom {
    @extend %flex-v;
    justify-content: space-between;
    padding: 20px 50px 40px;
    border-top: 1px solid rgba(90, 94, 98, 0.5);
    @include response(991px) {
      padding: 20px 20px 30px;
    }
    @include response(767px) {
      flex-direction: column-reverse;
      align-items: flex-start;
    }
  }
  &__account-socials {
    @extend %flex-v;
    ul {
      @extend %list;
      @extend %flex-v;
      li {
        @extend %flex-c;
        width: 24px;
        height: 24px;
        background: $brown;
        border-radius: 50%;
        margin-right: 5px;
        a {
          @extend %flex-c;
          width: 100%;
          height: 100%;
        }
        svg {
          transition: .3s ease-in-out;
        }
        @include hover() {
          svg {
            transform: scale(1.2);
          }
        }
      }
    }
  }
  &__account-socials-title {
    font-weight: 600;
    font-size: 12px;
    color: #ebebee;
    margin-right: 10px;
  }
  &__account-date {
    font-style: italic;
    font-size: 12px;
    color: #ebebee;
    @include response(767px) {
      margin-bottom: 16px;
    }
  }
  &__main {
    max-width: 100%;
    width: 100%;
    background: #fff;
    border: 1px solid #ebebee;
    padding: 50px 50px 75px;
    @include response(991px) {
      padding: 50px 20px;
      border: none;
      border-radius: 0;
    }
  }
  &__main-title {
    font-weight: 600;
    font-size: 27px;
    color: $black;
    margin-bottom: 20px;
    span {
      color: $green;
    }
    @include response(767px) {
      font-size: 22px;
      margin-bottom: 0;
    }
  }
  &__products {
    margin-bottom: 70px;
    @include response(767px) {
      margin-bottom: 30px;
    }
  }
  &__product {
    display: flex;
    align-items: flex-start;
    padding: 30px 0;
    border-bottom: 1px solid #ebebee;
    &:last-of-type {
      border-bottom: none;
    }
    @include response(767px) {
      flex-direction: column;
      &:last-of-type {
        border-bottom: 1px solid #ebebee;
      }
    }
  }
  &__product-left {
    max-width: 150px;
    width: 100%;
    flex-basis: 150px;
    margin-right: 50px;
    .submit-button {
      background: $black;
      @include hover() {
        background: rgba(11, 16, 20, 0.75);
      }
    }
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
      margin-bottom: 20px;
      display: flex;
      align-items: flex-start;
      margin: 0 0 25px;
      .submit-button {
        display: none;
      }
    }
  }
  &__product-right {
    max-width: 600px;
    width: 100%;
    flex-basis: 600px;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__product-image {
    @extend %flex-c;
    width: 100%;
    height: 240px;
    border: 1px solid #ebebee;
    margin-bottom: 20px;
    @include response(767px) {
      max-width: 80px;
      height: 128px;
      padding: 5px;
      margin: 0;
      img {
        height: 100%;
      }
    }
  }
  &__product-head {
    @extend %flex-v;
    margin-bottom: 18px;
    @include response(767px) {
      flex-direction: column;
      align-items: flex-start;
      margin-left: 20px;
    }
  }
  &__product-title {
    font-weight: 700;
    font-size: 19px;
    @include response(767px) {
      font-size: 16px;
      margin: 0 0 10px;
    }
    a {
      color: $black;
      text-decoration: none;
      @include hover() {
        text-decoration: underline;
      }
    }
  }
  &__product-date {
    font-size: 16px;
    color: #5a5e62;
    margin-left: auto;
    @include response(767px) {
      font-size: 14px;
      margin: 0 0 16px;
    }
  }
  &__product-rate {
    @extend %flex-v;
    margin-left: 32px;
    .rate {
      margin-bottom: 0;
    }
    @include response(767px) {
      margin: 0;
    }
  }
  &__product-rate-text {
    font-weight: 600;
    font-size: 18px;
    margin-right: 10px;
  }
  &__product-text {
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 7;
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    color: rgba(11, 16, 20, 0.9);
    margin-bottom: 12px;
    p {
      @include marginY()
    }
    @include response(767px) {
      -webkit-line-clamp: 99;
    }
  }
  &__screens {
    @extend %flex-v;
    flex-wrap: wrap;
    margin: 0 -5px 20px;
  }
  &__screen {
    background: #f9f9fa;
    @extend %flex-c;
    max-width: 142px;
    width: 100%;
    height: 80px;
    margin: 0 5px 10px;
  }
  &__bottom {
    @extend %flex-v;
    justify-content: space-between;
  }
  &__product-help {
    font-size: 15px;
    color: rgba(90, 94, 98, 0.9);
  }
  &__product-mark {
    @extend %flex-v;
    justify-content: space-between;
    width: 100px;
    height: 36px;
    border: 1px solid #ebebee;
    border-radius: 5px;
    small {
      @extend %flex-c;
      width: 50px;
      height: 100%;
      font-size: 12px;
      color: #5a5e62;
      cursor: pointer;
    }
    span {
      @extend %flex-c;
      width: 50px;
      height: 100%;
      background: #f9f9fa;
      font-size: 12px;
      color: #5a5e62;
    }
  }
}