.error-page {
  padding: 100px 0 200px;
  @include response(767px) {
    padding: 30px 20px 80px;
  }
  &__inner {
    max-width: 640px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__icon {
    margin-bottom: 102px;
    @include response(767px) {
      display: none;
    }
  }
  &__subtitle {
    font-weight: 600;
    font-size: 22px;
    color: $green;
    margin-bottom: 15px;
  }
  &__title {
    font-family: $Tfont;
    font-weight: 700;
    font-size: 54px;
    margin-bottom: 30px;
    text-align: center;
    @include response(767px) {
      font-size: 34px;
    }
  }
  &__text {
    font-size: 18px;
    line-height: 175%;
    color: #5a5e62;
    text-align: center;
    margin-bottom: 50px;
    @include response(767px) {
      margin-bottom: 30px;
    }
  }
  &__search {
    max-width: 430px;
    width: 100%;
    margin-bottom: 30px;
    position: relative;
  }
  &__search-input {
    width: 100%;
    height: 55px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    padding: 0 50px 0 16px;
    font-size: 16px;
    color: #5a5e62;
    &::placeholder {
      color: rgba(90, 94, 98, 0.5);
    }
  }
  &__search-button {
    @extend %flex-c;
    position: absolute;
    right: 13px;
    top: 50%;
    transform: translateY(-50%);
    width: 32px;
    height: 30px;
    background: transparent;
    border: 1px solid #5a5e62;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
    svg {
      transition: .3s ease-in-out;
    }
    @include hover() {
      svg {
        transform: scale(1.2);
      }
    }
  }
}