body.article {
  .wrapper {
    overflow: visible;
    @include response(1023px) {
      overflow: hidden;
    }
  }
}

.article {
  &__inner {
    position: relative;
    max-width: 1030px;
    width: 100%;
    margin: 0 auto 30px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    overflow: visible;
    @include response(1100px) {
      padding: 0 20px;
    }
    @include response(1023px) {
      flex-direction: column;
    }
  }
  &__main {
    max-width: 680px;
    width: 100%;
    flex-basis: 680px;
    @include response(1023px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__aside {
    position: static;
    top: 100px;
    max-width: 300px;
    width: 100%;
    flex-basis: 300px;
    height: auto;
    margin-left: 20px;
    @media (min-height: 1020px), screen and (orientation: portrait) {
      position: sticky;
    }
    @include response(1023px) {
      display: none;
    }
  }
  &__title {
    font-family: $Tfont;
    font-weight: 700;
    font-size: 32px;
    line-height: 150%;
    margin-bottom: 20px;
    @include response(767px) {
      font-size: 28px;
      margin-bottom: 20px;
    }
  }
  &__head {
    @extend %flex-v;
    margin-bottom: 30px;
    @include response(767px) {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 30px;
    }
  }
  &__date {
    position: relative;
    @extend %flex-v;
    font-size: 12px;
    color: #5a5e62;
    gap: 5px;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: -30px;
      transform: translateY(-50%);
      width: 1px;
      height: 16px;
      background: #bfc1c4;
    }
    span {
      font-weight: 600;
      color: $black;
    }
    @include response(767px) {
      margin-bottom: 15px;
      &::after {
        display: none;
      }
    }
  }
  &__author {
    @extend %flex-v;
    font-size: 12px;
    color: #5a5e62;
    margin-left: 60px;
    margin-right: auto;
    span {
      font-weight: 600;
      color: $black;
    }
    svg {
      margin-right: 6px;
    }
    @include response(767px) {
      margin: 0 0 15px;
    }
  }
  &__anchor {
    @extend %flex-c;
    padding: 6px 11px;
    background: #f9f9fa;
    border: 1px solid #ebebee;
    border-radius: 5px;
    font-weight: 500;
    font-size: 14px;
    color: $black;
    text-decoration: none;
    cursor: pointer;
    transition: .3s ease-in-out;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: #ebebee;
    }
  }
  &__socials {
    @extend %list;
    @extend %flex-v;
    margin: 0 -2px;
    li {
      width: 32px;
      height: 32px;
      margin: 0 2px;
      border: 1px solid $brown;
      border-radius: 50%;
      transition: .3s ease-in-out;
      a {
        width: 100%;
        height: 100%;
        @extend %flex-c;
      }
      svg {
        fill: #5a5e62;
        transition: .3s ease-in-out;
      }
      @include hover() {
        background: $brown;
        svg {
          fill: #fff;
          transform: scale(1.1);
        }
      }
    }
  }
  &__image {
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 40px;
    img {
      width: 100%;
    }
    @include response(767px) {
      margin: 0 -20px 30px;
    }
  }
  &__content {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    color: $black;
    margin-bottom: 30px;
    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 175%;
      color: $black;
    }
    h2 {
      font-weight: 600;
      font-size: 27px;
      line-height: 150%;
      margin: 20px 0;
      @include response(767px) {
        font-size: 22px;
      }
    }
    h3 {
      font-weight: 600;
      font-size: 24px;
      line-height: 150%;
      @include response(767px) {
        font-size: 20px;
      }
    }
    h4 {
      font-weight: 600;
      font-size: 22px;
      line-height: 150%;
      @include response(767px) {
        font-size: 19px;
      }
    }
    h5 {
      font-weight: 500;
      font-size: 20px;
      line-height: 150%;
      margin: 20px 0;
      @include response(767px) {
        font-size: 18px;
      }
    }
    table {
      width: 100%;
      border-collapse: collapse;
      border: 1px solid #ebebee;
      @include response(680px) {
        width: 680px;
      }
      thead {
        tr {
          td {
            padding: 5px 15px;
            font-weight: 600;
            font-size: 15px;
            color: $black;
            text-align: center;
            background: #ebebee;
            box-shadow: inset 0px -1px 0px #ebebee;
            border: 1px solid #ebebee;
            height: 65px;
          }
        }
      }
      tbody {
        tr {
          td {
            border-bottom: 1px solid #ebebee;
            width: 25%;
            text-align: center;
            font-size: 15px;
            height: 65px;
            &:first-child {
              background: rgba(249, 249, 250, 0.5);
            }
          }
        }
      }
    }
    a {
      color: $green;
      text-decoration: none;
      font-size: 15px;
      transition: .3s ease-in-out;
      @include hover() {
        color: $pink;
        text-decoration: none;
      }
    }
    s {
      text-decoration-color: $green;
    }
    span {
      color: $green;
    }
    q {
      position: relative;
      display: block;
      font-style: italic;
      font-weight: 400;
      font-size: 15px;
      line-height: 175%;
      padding: 0 0 0 40px;
      margin: 30px 0;
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        background-image: url('../data/quote.svg');
        background-position: center;
        background-repeat: no-repeat;
        width: 24px;
        height: 18px;
      }
      &::after {
        content: '';
        position: absolute;
        left: 10px;
        bottom: 5px;
        width: 3px;
        height: calc(100% - 38px);
        background: $green;
        border-radius: 5px;
      }
    }
    ol {
      @extend %list;
      counter-reset: item;
      li {
        position: relative;
        color: #5a5e62;
        &::before {
          content: counter(item) ". ";
          counter-increment: item;
          color: $green;
          font-weight: 600;
        }
        span {
          color: $black;
        }
      }
    }
    .column-text {
      display: flex;
      margin: 0 -20px;
      div {
        margin: 0 20px;
      }
      @include response(767px) {
        flex-wrap: wrap;
      }
    }
    table.table-compare {
      width: 100%;
      border-collapse: collapse;
      border: 1px solid #ebebee;
      margin: 40px 0;
      @include response(680px) {
        width: 680px;
      }
      thead {
        tr {
          td {
            padding: 5px 15px;
            font-weight: 600;
            font-size: 15px;
            color: $black;
            text-align: center;
            background: transparent;
            border: none;
            height: 65px;
          }
        }
      }
      tbody {
        tr {
          td {
            border: 1px solid #ebebee;
            width: 50%;
            font-size: 15px;
            padding: 30px;
            text-align: left;
            vertical-align: top;
            &:first-child {
              background: transparent;
            }
            ul {
              @extend %list;
              li {
                @extend %flex-v;
                margin-bottom: 16px;
                &:last-of-type {
                  margin-bottom: 0;
                }
                svg {
                  margin-right: 15px;
                }
              }
            }
          }
        }
      }
    }
  }
  &__bottom {
    @extend %flex-v;
    justify-content: space-between;
    @include response(767px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__bottom-title {
    font-size: 18px;
    @include response(767px) {
      margin-bottom: 20px;
    }
  }
  &__recipe {
    padding: 50px 40px 60px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    margin-bottom: 40px;
    @include response(767px) {
      margin: 0 -20px 40px;
      border-radius: 0;
      padding: 40px 20px 30px;
    }
  }
  &__recipe-main {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 30px;
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__recipe-content {
    max-width: 352px;
    width: 100%;
    flex-basis: 352px;
    margin-right: 20px;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
      margin: 0 0 30px;
    }
  }
  &__recipe-image {
    max-width: 218px;
    width: 100%;
    flex-basis: 218px;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
      img {
        width: 100%;
      }
    }
  }
  &__recipe-title {
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    margin-bottom: 14px;
  }
  &__recipe-head {
    @extend %flex-v;
    margin: 0 -15px 30px;
    @include response(767px) {
      margin: 0 -15px 25px;
    }
  }
  &__recipe-option {
    position: relative;
    font-size: 12px;
    color: #5a5e62;
    margin: 0 15px;
    span {
      font-weight: 600;
      color: $black;
    }
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -15px;
      background: #bfc1c4;
      height: 16px;
      width: 1px;
    }
    &:last-of-type {
      &::after {
        display: none;
      }
    }
    @include response(767px) {
      margin: 0 10px;
      &::after {
        right: -10px;
      }
    }
  }
  &__recipe-text {
    font-size: 16px;
    line-height: 175%;
    color: rgba(11, 16, 20, 0.9);
    margin-bottom: 20px;
  }
  &__recipe-rate {
    @extend %flex-v;
    .rate {
      margin: 0 10px 5px;
    }
  }
  &__recipe-mark {
    font-weight: 600;
    font-size: 18px;
  }
  &__recipe-votes {
    font-size: 14px;
    color: #5a5e62;
  }
  &__recipe-buttons {
    @extend %flex-v;
    justify-content: space-between;
    margin: 0 -4px 10px;
    @include response(767px) {
      margin: 0 -4px;
    }
  }
  &__recipe-button {
    @extend %flex-c;
    max-width: 290px;
    width: 100%;
    height: 50px;
    background: #f9f9fa;
    border: 1px solid #ebebee;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
    font-weight: 500;
    font-size: 14px;
    color: $black;
    transition: .3s ease-in-out;
    margin: 0 4px;
    svg {
      fill: #5a5e62;
      margin-right: 10px;
    }
    @include hover() {
      background: #ebebee;
    }
  }
  &__recipe-box {
    padding: 30px 0;
    border-bottom: 1px solid #ebebee;
    &:last-of-type {
      border-bottom: none;
    }
  }
  &__recipe-subtitle {
    font-weight: 700;
    font-size: 19px;
    margin-bottom: 30px;
  }
  &__recipe-info {
    font-size: 16px;
    line-height: 175%;
    ol {
      @extend %list;
      counter-reset: item;
      li {
        padding: 0 0 0 20px;
        font-size: 16px;
        line-height: 175%;
        position: relative;
        color: #0B1014;
        margin-bottom: 20px;
        &:last-of-type {
          margin-bottom: 0;
        }
        &::before {
          position: absolute;
          left: 0;
          content: counter(item) ". ";
          counter-increment: item;
          color: $green;
          font-weight: 600;
        }
      }
    }
    small {
      display: block;
      font-weight: 400;
      font-size: 14px;
      line-height: 175%;
      color: rgba(11, 16, 20, 0.7);
      margin: 20px 0 16px;
    }
  }
  &__recipe-options {
    @extend %flex-v;
    margin: 0 -15px;
    .article__recipe-option {
      font-size: 14px;
    }
  }
  &__checkbox {
    margin-bottom: 22px;
    &:last-of-type {
      margin-bottom: 0;
    }
    label {
      input {
        display: none;
      }
      input + span {
        position: relative;
        display: block;
        padding-left: 32px;
        cursor: pointer;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 3px;
          width: 20px;
          height: 20px;
          border-radius: 50%;
          border: 1px solid rgba(0, 0, 0, 0.2);
        }
        &::after {
          content: '';
          position: absolute;
          left: 0;
          top: 3px;
          width: 20px;
          height: 20px;
          border-radius: 50%;
          border: 1px solid $green;
          background-color: $green;
          background-image: url('../data/check-white.svg');
          background-repeat: no-repeat;
          background-position: center;
          visibility: hidden;
          opacity: 0;
          overflow: hidden;
        }
      }
      input:checked + span {
        &::after {
          visibility: visible;
          opacity: 1;
          overflow: visible;
        }
      }
    }
  }
  &__recipe-insta {
    @extend %flex-v;
    background: #f9f9fa;
    border-radius: 5px;
    padding: 22px 20px 22px 28px;
    @include response(767px) {
      padding: 20px;
      align-items: flex-start;
    }
  }
  &__recipe-insta-icon {
    width: 46px;
    height: 46px;
    flex-shrink: 0;
    margin-right: 20px;
    @include response(767px) {
      margin-right: 15px;
    }
  }
  &__recipe-insta-title {
    font-weight: 400;
    font-size: 16px;
    a {
      color: $green;
    }
  }
}

.side-stories {
  background: #f9f9fa;
  border: 1px solid #deb992;
  border-radius: 5px;
  padding: 30px;
  &__title {
    font-weight: 500;
    font-size: 15px;
    margin-bottom: 25px;
  }
  &__item {
    @extend %flex-v;
    margin-bottom: 25px;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
  &__item-image {
    width: 80px;
    flex-shrink: 0;
    margin-right: 15px;
    overflow: hidden;
    border-radius: 5px;
    img {
      transition: .3s ease-in-out;
    }
    @include hover() {
      img {
        transform: scale(1.05);
      }
    }
  }
  &__item-content {
    max-width: 145px;
    width: 100%;
    flex-basis: 145px;
  }
  &__item-title {
    font-weight: 500;
    font-size: 14px;
    margin-bottom: 8px;
    a {
      color: $black;
      text-decoration: none;
      @include hover() {
        text-decoration: underline;
      }
    }
  }
  &__item-date {
    @extend %flex-v;
    font-size: 12px;
    color: rgba(90, 94, 98, 0.8);
    svg {
      margin-right: 7px;
      margin-top: -2px;
    }
  }
}

.side-instagram {
  background: #f9f9fa;
  border: 1px solid #deb992;
  border-radius: 5px;
  padding: 30px;
  margin-top: 15px;
  @include response(1023px) {
    margin: 0 20px 50px;
  }
  &__title {
    @extend %flex-v;
    font-weight: 500;
    font-size: 15px;
    margin-bottom: 20px;
    svg {
      margin-right: 10px;
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -5px;
  }
  &__item {
    max-width: calc(50% - 10px);
    width: 100%;
    flex-basis: calc(50% - 10px);
    margin: 0 5px 10px;
    img {
      width: 100%;
    }
    @include response(1023px) {
      max-width: calc(25% - 10px);
      flex-basis: calc(25% - 10px);
    }
    @include response(991px) {
      max-width: calc(33.33333% - 10px);
      flex-basis: calc(33.33333% - 10px);
    }
    @include response(580px) {
      max-width: calc(50% - 10px);
      flex-basis: calc(50% - 10px);
    }
  }
}

.more-stories {
  padding: 25px 0 50px;
  @include response(1200px) {
    padding: 25px 20px 50px;
  }
  @include response(767px) {
    padding: 0 20px 10px;
  }
  &__inner {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
  }
  &__title {
    font-weight: 600;
    font-size: 32px;
    margin-bottom: 85px;
    @include response(767px) {
      margin-bottom: 20px;
      font-size: 24px;
    }
  }
  &__list {
    display: flex;
    margin: 0 -20px;
    @include response(579px) {
      margin: 0;
    }
    .stories__item {
      max-width: calc(25% - 40px);
      flex-basis: calc(25% - 40px);
      @include response(991px) {
        max-width: calc(33.33333% - 40px);
        flex-basis: calc(33.33333% - 40px);
      }
      @include response(767px) {
        max-width: calc(50% - 40px);
        flex-basis: calc(50% - 40px);
      }
      @include response(579px) {
        max-width: 260px;
        flex-basis: 260px;
      }
    }
  }
}

.article-comment {
  margin: 5px 0 100px;
  @include response(1023px) {
    margin: 5px 0 50px;
  }
  &__inner {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include response(1200px) {
      padding: 0 20px;
    }
    @include response(1023px) {
      flex-direction: column;
    }
  }
  &__holder {
    max-width: 748px;
    width: 100%;
    .reviews-holder {
      margin-bottom: 90px;
    }
    .review {
      padding-bottom: 0;
    }
  }
  &__main {
    max-width: 748px;
    width: 100%;
    flex-basis: 748px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    padding: 50px 40px;
    margin-right: 20px;
    @include response(1023px) {
      max-width: 100%;
      flex-basis: auto;
      margin: 0 auto 50px;
    }
    @include response(767px) {
      padding: 50px 20px;
    }
  }
  &__aside {
    max-width: 372px;
    width: 100%;
    flex-basis: 372px;
    @include response(1023px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 32px;
    margin-bottom: 10px;
    @include response(767px) {
      text-align: center;
      font-size: 22px;
      margin-bottom: 18px;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 155%;
    color: rgba(90, 94, 98, 0.9);
    margin-bottom: 25px;
    @include response(767px) {
      text-align: center;
    }
  }
  &__form {
    .main-input {
      height: 55px;
      font-size: 16px;
      @include response(767px) {
        height: 40px;
      }
    }
    .main-textarea {
      font-size: 16px;
      padding: 15px;
    }
  }
}

.other-recipes {
  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    margin-bottom: 30px;
  }
  &__list {
    .side-stories {
      &__item {
        margin-bottom: 30px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
      &__item-image {
        width: 132px;
      }
      &__item-content {
        max-width: 220px;
        flex-basis: 220px;
      }
      &__item-title {
        font-weight: 500;
        font-size: 16px;
        margin-bottom: 10px;
      }
    }
  }
}

.blog-section {
  padding: 50px 0 0;
  border-bottom: 1px solid #ebebee;
  margin-bottom: 50px;
  @include response(1200px) {
    padding: 50px 20px 0;
  }
  @include response(767px) {
    padding: 30px 20px 0;
    margin-bottom: 0;
  }
  &__inner {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include response(767px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__content {
    max-width: 560px;
    width: 100%;
    flex-basis: 560px;
    @include response(767px) {
      flex-basis: auto;
      max-width: 100%;
      margin-bottom: 30px;
      text-align: center;
    }
    @include response(520px) {
      text-align: left;
    }
  }
  &__image {
    max-width: 430px;
    width: 100%;
    flex-basis: 430px;
    @include response(767px) {
      flex-basis: auto;
      max-width: 100%;
      text-align: center;
    }
  }
  &__title {
    font-family: $Tfont;
    font-weight: 700;
    font-size: 54px;
    line-height: 125%;
    margin-bottom: 30px;
    @include response(767px) {
      font-size: 34px;
      margin-bottom: 20px;
    }
  }
  &__subtitle {
    font-size: 18px;
    line-height: 175%;
    margin-bottom: 20px;
    @include response(767px) {
      margin-bottom: 0;
    }
  }
  &__text {
    font-size: 15px;
    line-height: 175%;
    @include response(767px) {
      display: none;
    }
  }
}

.blog-slider {
  max-width: 1240px;
  width: 100%;
  margin: 0 auto;
  background: #fff;
  border: 1px solid #ebebee;
  border-radius: 5px;
  &__inner {
    padding: 75px 0;
    @include response(767px) {
      padding: 40px 0 50px;
    }
  }
  &__bottom {
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
    @extend %flex-v;
    @include response(991px) {
      padding: 0 20px;
    }
  }
  &__head {
    display: none;
    @include response(991px) {
      display: block;
      padding: 0 20px;
      margin-bottom: 30px;
      text-align: center;
    }
  }
  &__head-title {
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    margin-bottom: 15px;
  }
  &__head-text {
    font-size: 15px;
    line-height: 175%;
  }
  &__slider {
    margin-bottom: 85px;
    @include response(767px) {
      margin-bottom: 60px;
    }
  }
  &__slide {
    max-width: 170px;
    width: 100%;
    margin: 0 22px;
    &--content {
      padding-top: 55px;
      max-width: 255px;
      @include response(991px) {
        display: none !important;
      }
    }
  }
  &__slide-image {
    margin-bottom: 25px;
    img {
      width: 100%;
    }
  }
  &__slide-title {
    font-family: $Tfont;
    font-weight: 700;
    font-size: 16px;
    line-height: 125%;
    color: #000;
    text-align: center;
  }
  &__progress {
    display: block;
    width: 100%;
    height: 2px;
    border-radius: 10px;
    overflow: hidden;
    background-color: #ebebee;
    background-image: linear-gradient(to right, rgba(90, 94, 98, 0.3), rgba(90, 94, 98, 0.3));
    background-repeat: no-repeat;
    background-size: 0 100%;
    transition: background-size .4s ease-in-out;
  }
  &__navigation {
    @extend %flex-c;
    width: 66px;
    flex-shrink: 0;
    margin-left: 30px;
    @include response(767px) {
      display: none;
    }
  }
  &__arrow {
    @extend %flex-c;
    width: 28px;
    height: 28px;
    border: 1px solid #ebebee;
    border-radius: 50%;
    cursor: pointer;
    transition: .3s ease-in-out;
    @include hover() {
      background: #ebebee;
    }
    &.disabled {
      pointer-events: none;
    }
  }
  &__next {
    margin-left: 10px;
  }
  &__content-title {
    max-width: 220px;
    width: 100%;
    font-family: $Tfont;
    font-weight: 700;
    font-size: 27px;
    line-height: 150%;
    color: #000;
    margin-bottom: 16px;
  }
  &__content-text {
    max-width: 220px;
    width: 100%;
    font-size: 15px;
    line-height: 175%;
  }
}

.blog-posts {
  padding: 50px 0 75px;
  max-width: 1230px;
  width: 100%;
  margin: 0 auto;
  @include response(1280px) {
    padding: 50px 20px 75px;
  }
  @include response(1200px) {
    padding: 50px 20px 30px;
  }
  &__list {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 0 -37px;
    @include response(1200px) {
      margin: 0 -15px;
    }
  }
  &__item {
    max-width: calc(33.33333% - 74px);
    width: 100%;
    flex-basis: calc(33.33333% - 74px);
    margin: 0 37px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    padding: 25px 25px 35px;
    @include response(1200px) {
      max-width: calc(33.33333% - 30px);
      flex-basis: calc(33.33333% - 30px);
      margin: 0 15px 20px;
    }
    @include response(991px) {
      max-width: calc(50% - 30px);
      flex-basis: calc(50% - 30px);
    }
    @include response(640px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__image {
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 16px;
    img {
      width: 100%;
      transition: .3s ease-in-out;
    }
    @include hover() {
      img {
        transform: scale(1.05);
      }
    }
  }
  &__date {
    @extend %flex-v;
    font-size: 12px;
    color: rgba(90, 94, 98, 0.8);
    margin-bottom: 24px;
    svg {
      margin: 0 6px 3px 0;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    margin-bottom: 28px;
    a {
      color: #0b1014;
      text-decoration: none;
      transition: .3s ease-in-out;
      @include hover() {
       color: #1ba098;
      }
    }
    @include response(640px) {
      font-size: 18px;
    }
  }
  &__link {
    font-weight: 600;
    font-size: 12px;
    color: $green;
    text-decoration: none;
    svg {
      margin-left: 6px;
      @include transition();
    }
    &:hover {
      svg {
        transform: translateX(5px);
      }
    }
  }
}

.blog-block {
  padding: 90px 0 62px;
  .pagination {
    margin: 60px auto 0;
  }
  &--white {
    background: #fff;
  }
  @include response(1280px) {
    padding:90px 20px 62px;
  }
  @include response(767px) {
    padding: 50px 20px 20px;
    .pagination {
      margin: 0 auto 30px;
    }
  }
  &__inner {
    max-width: 1240px;
    width: 100%;
    margin: 0 auto;
  }
  &__title {
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    text-align: center;
    margin-bottom: 90px;
    @include response(767px) {
      font-size: 22px;
      margin-bottom: 40px;
    }
  }
  &__main {
    width: 100%;
    display: flex;
    align-items: stretch;
    background: #fff;
    border: 1px solid #ebebee;
    margin-bottom: 75px;
    &--lg {
      background: #f9f9fa;
      .blog-block__main-image {
        max-width: 600px;
        flex-basis: 600px;
        @include response(991px) {
          max-width: 100%;
          flex-basis: auto;
        }
      }
      .blog-block__main-content {
        max-width: 640px;
        flex-basis: 640px;
        @include response(991px) {
          max-width: 100%;
          flex-basis: auto;
        }
        @include response(767px) {
          padding: 20px 20px 30px;
        }
      }
    }
    @include response(991px) {
      flex-direction: column;
      margin-bottom: 50px;
    }
    @include response(767px) {
      margin-bottom: 40px;
    }
  }
  &__main-image {
    max-width: 443px;
    width: 100%;
    flex-basis: 443px;
    overflow: hidden;
    img {
      height: 100%;
      transition: .3s ease-in-out;
    }
    @include hover() {
      img {
        transform: scale(1.05);
      }
    }
    @include response(991px) {
      max-width: 100%;
      flex-basis: auto;
      img {
        width: 100%;
      }
    }
  }
  &__main-content {
    position: relative;
    max-width: 797px;
    width: 100%;
    flex-basis: 797px;
    padding: 60px 40px 30px;
    display: flex;
    align-items: flex-start;
    @include response(991px) {
      max-width: 100%;
      flex-basis: auto;
    }
    @include response(767px) {
      padding: 60px 20px 30px;
    }
  }
  &__main-left {
    max-width: 117px;
    width: 100%;
    flex-basis: 117px;
    margin-right: 40px;
    @include response(767px) {
      position: absolute;
      z-index: 2;
      max-width: 305px;
      width: 100%;
      display: flex;
      margin: 0;
      left: 50%;
      transform: translateX(-50%);
      top: -25px;
    }
  }
  &__main-right {
    max-width: 430px;
    width: 100%;
    flex-basis: 430px;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__main-head {
    @extend %flex-v;
    margin-bottom: 15px;
  }
  &__main-featured {
    font-weight: 600;
    font-size: 12px;
    color: $green;
    margin-right: 20px;
    svg {
      margin-right: 7px;
    }
  }
  &__main-date {
    font-weight: 400;
    font-size: 12px;
    color: rgba(90, 94, 98, 0.8);
    svg {
      margin-right: 7px;
    }
  }
  &__main-title {
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    margin-bottom: 30px;
    @include transition();
    &:hover {
      color: #1ba098;
    }
    @include response(767px) {
      font-size: 22px;
      margin-bottom: 20px;
    }
  }
  &__main-text {
    font-weight: 400;
    font-size: 15px;
    line-height: 175%;
    margin-bottom: 30px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    @include response(767px) {
      margin-bottom: 20px;
      -webkit-line-clamp: 99;
    }
  }
  &__main-link {
    @extend %flex-v;
    font-weight: 600;
    font-size: 14px;
    color: $green;
    text-decoration: none;
    svg {
      margin-left: 7px;
      transition: .3s ease-in-out;
    }
    @include hover() {
      svg {
        margin-left: 10px;
      }
    }
  }
  &__main-info {
    width: 100%;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    margin-bottom: 10px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include response(767px) {
      margin: 0 4px;
    }
  }
  &__main-info-title {
    @extend %flex-v;
    font-weight: 600;
    font-size: 12px;
    margin-bottom: 10px;
    svg {
      margin-right: 7px;
    }
  }
  &__main-info-value {
    font-size: 12px;
  }
  &__posts {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
  }
  &__list {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -20px;
    @include response(991px) {
      justify-content: flex-start;
    }
    .stories__item {
      max-width: calc(25% - 40px);
      flex-basis: calc(25% - 40px);
      @include response(991px) {
        max-width: calc(33.33333% - 40px);
        flex-basis: calc(33.33333% - 40px);
      }
      @include response(767px) {
        max-width: calc(50% - 40px);
        flex-basis: calc(50% - 40px);
      }
      @include response(580px) {
        max-width: 100%;
        flex-basis: auto;
      }
    }
    .stories__title {
      font-size: 18px;
      font-weight: 500;
      line-height: 140%;
      a {
        transition: .3s ease-in-out;
        @include hover() {
          color: $green;
        }
      }
    }
  }
}