.learning-holder {
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto 80px;
  display: flex;
  justify-content: space-between;
  @include response(992px) {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.learning-content {
  max-width: 750px;
  width: 100%;
  .content-area {
    margin-bottom: 50px;
    p a {
      text-decoration: none;
      color: #1ba098;
      font-weight: 500;
    }
    blockquote {
      font-size: 15px;
      line-height: 175%;
      font-style: italic;
      margin: 0 0 30px;
      padding-left: 40px;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 24px;
        height: 18px;
        background-image: url("../data/quote.png");
        background-repeat: no-repeat;
      }
      &::after {
        content: '';
        position: absolute;
        left: 10px;
        top: 25px;
        height: calc(100% - 30px);
        border-right: 3px solid #1ba098;
      }
    }
    p {
      margin: 0 0 30px;
      line-height: 175%;
    }
    ul {
      padding: 0 20px;
      margin: 0;
      list-style: none;
      li {
        position: relative;
        padding-left: 24px;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 2px;
          width: 16px;
          height: 24px;
          background-image: url("../data/list-marker.svg");
        }
      }
      h2,h3,h4,h5,h6 {
        margin: 0 0 10px;
      }
    }
  }
  @include response(992px) {
    max-width: 100%;
  }
}
.learning-aside {
  max-width: 340px;
  width: 100%;
  margin-left: 30px;
  position: relative;
  padding-bottom: 50px;
  @include response(992px) {
    margin: 0 auto;
    .featured-bottles {
      display: none;
    }
  }
  &__inner {
    position: sticky;
    top: 100px;
    left: 0;
  }
}
.learning-nav {
  padding: 0;
  margin: 0 0 50px;
  list-style: none;
  border-radius: 5px;
  border: 2px solid #DEB992;
  overflow: hidden;
  &-item {
    display: flex;
    align-items: center;
    padding: 20px 18px;
    gap: 14px;
    border-bottom: 1px solid #fff;
    background: #f9f9fa;
    text-decoration: none;
    &:hover,
    &.active {
      background: #DEB992;
      border-color: #DEB992;
    }
    @include transition();
    &__label {
      font-size: 12px;
      line-height: 125%;
      color: #5A5E62;
    }
    &__title {
      font-family: 'Playfair Display', sans-serif;
      font-weight: 700;
      font-size: 22px;
      line-height: 125%;
      color: #0B1014;
    }
  }
}
.agave-item {
  background: #f9f9fa;
  margin-bottom: 30px;
  &__img {
    padding: 30px 30px 40px;
    position: relative;
    margin-bottom: 40px;
    display: flex;
    justify-content: center;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 40px;
      background-image: url("../data/agave-line-2.png");
    }
  }
  &:nth-child(even) {
    .agave-item__img::after {
      background-image: url("../data/agave-line-1.png");
    }
  }
  &__inner {
    padding: 0 30px 30px;
  }
  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 25px;
    text-align: center;
    margin-bottom: 20px;
  }
  &__content {
    p {
      margin: 0 0 30px;
      font-size: 16px;
      line-height: 175%;
      &:last-child {
        margin: 0;
      }
    }
  }
}

.what-is-mezcal,
.mezcal-vs-tquila,
.how-to-drink,
.affordable-mezcal,
.worm-mezcal,
.mezcal-coctails {
  overflow: visible;
}

.mezcal-vs-tquila,
.mezcal-coctails {
  @include response(767px) {
    .learning-heading__bg-left {
      margin-left: -40px;
    }
    .learning-heading__bg-right {
      margin-right: -50px;
    }
  }
}

.how-to-drink {
  .learning-heading__bg-left {
    margin-bottom: -70px;
    @include response(992px) {
      margin-bottom: -20px;
      margin-right: -30px;
      img {
        max-width: 150px;
        margin-left: -30px;
      }
    }
  }
  .learning-heading__content {
    margin: 40px -25px 80px;
  }
}

.affordable-mezcal {
  .learning-heading__bg-left,
  .learning-heading__bg-right {
    margin-bottom: -15px;
  }
  .learning-heading::after {
    background-image: url("../data/menu-line-2.png");
  }
  @include response(767px) {
    .learning-heading__bg-left {
      margin-left: -20px;
    }
    .learning-heading__bg-right {
      margin-right: -20px;
    }
  }
}

.worm-mezcal {
  .learning-heading {
    padding-top: 0;
    position: relative;
  }
  .learning-heading__content {
    margin: 80px 30px 0;
    flex-shrink: 0;
  }
  @include response(767px) {
    .learning-heading__inner {
      justify-content: center;
    }
    .learning-heading__content {
      position: relative;
      z-index: 2;
    }
    .learning-heading__bg-left,
    .learning-heading__bg-right {
      position: absolute;
      max-width: 200px !important;
      bottom: 20px;
    }
    .learning-heading__bg-left {
      left: -120px;
    }
    .learning-heading__bg-right {
      right: -120px;
    }
  }
}

.mezcal-coctails {
  .learning-heading__content {
    margin-bottom: 80px;
    max-width: 550px;
  }
  .learning-heading::after {
    background-image: url("../data/menu-line-2.png");
  }
}

.mezcal-vs-tquila {
  .learning-heading::after {
    background-image: url("../data/menu-line-2.png");
  }
  .learning-heading__bg-left {
    margin-bottom: -20px;
  }
  .learning-heading__bg-right {
    margin-bottom: -20px;
  }
}

.featured-bottles {
  &__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 125%;
    margin-bottom: 18px;
  }
  .bottles-list {
    margin: 0 -8px;
    .best-products__item {
      max-width: calc(50% - 16px);
      flex-basis: calc(50% - 16px);
      margin: 0 8px 16px;
      padding: 20px 16px 0;
      &-rate {
        font-size: 14px;
      }
      .rate__item {
        width: 12px;
      }
    }
  }
}
.aging-item {
  padding: 30px 40px;
  background: #DEB992;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 20px;
  @include response(767px) {
    padding: 20px 15px;
  }
  &__inner {
    display: flex;
    align-items: center;
    gap: 35px;
    margin-bottom: 24px;
    @include response(767px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__footer {
    display: flex;
    justify-content: center;
  }
  &__left {
    padding-left: 80px;
    position: relative;
    @include response(767px) {
      padding: 0;
    }
  }
  &__title {
    font-family: 'Playfair Display', sans-serif;
    font-weight: 700;
    font-size: 32px;
    line-height: 125%;
    transform: rotate(-90deg) translate(-2%, -138%);
    position: absolute;
    left: 0;
    top: 31px;
    width: 200px;
    transform-origin: 50% 50%;
    text-align: center;
    @include response(767px) {
      transform: none;
      position: static;
    }
  }
  &__img {
    @include response(767px) {
      display: none;
    }
  }
  &__right {
    display: flex;
    width: 100%;
    border-radius: 5px;
    overflow: hidden;
    @include response(767px) {
      flex-wrap: wrap;
    }
  }
  &__info {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
    padding: 20px 25px;
    padding-right: 0;
    background: #fff;
    @include response(767px) {
      padding: 10px;
      justify-content: space-between;
    }
    &-block {
      width: 100%;
      max-width: calc(50% - 25px);
      &-title {
        font-weight: 700;
        font-size: 15px;
        line-height: 15px;
        margin-bottom: 5px;
      }
      &-content {
        font-style: italic;
        font-weight: 500;
        font-size: 13px;
        line-height: 13px;
        color: #5A5E62;
      }
    }
  }
  &__usage {
    max-width: 164px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #000;
    color: #DEB992;
    padding: 20px 15px;
    font-size: 12px;
    text-align: center;
    svg {
      margin-bottom: 12px;
    }
    @include response(767px) {
      max-width: 100%;
      width: 100%;
      flex-direction: row;
      justify-content: center;
      text-align: left;
      gap: 15px;
      .aging-item__usage-text {
        max-width: 190px;
      }
    }
  }
  &.yellow {
    background: #EF9D1B;
    .aging-item__usage {
      color: #EF9D1B;
      svg path {
        fill: #EF9D1B;
      }
    }
  }
  &.orange {
    background: #D85D19;
    .aging-item__usage {
      color: #D85D19;
      svg path {
        fill: #D85D19;
      }
    }
  }
}
.mezcal-table {
  overflow: hidden;
  border-radius: 5px;
  table {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
    border: 1px solid #EBEBEE;
    td {
      padding: 19px;
      font-size: 12px;
    }
    tbody tr:nth-child(odd) {
      td {
        background: #f9f9fa;
      }
    }
    thead {
      td {
        font-weight: 700;
        font-size: 12px;
        background: #DEB992;
        border-right: 1px solid #fff;
        padding: 16px;
        &:last-child {
          border: none;
        }
      }
    }
  }
}
.learning-author {
  padding: 80px 15px;
}
.learning-stories {
  .stories__head-title {
    text-align: left;
  }
  .stories__list {
    margin-bottom: 0;
  }
  .stories__inner {
    margin: 0 auto 100px;
  }
  @include response(767px) {
    .stories__item {
      max-width: 260px !important;
      flex-basis: 260px !important;
    }
  }
}

.type-item {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  padding: 30px;
  background: rgba(222, 185, 146, 0.14);
  border: 2px solid #DEB992;
  border-radius: 5px;
  margin-bottom: 30px;
  @include response(767px) {
    padding: 20px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__thumb {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-right: 30px;
    margin-right: 30px;
    border-right: 2px solid #DEB992;
    max-width: 145px;
    width: 100%;
    @include response(767px) {
      flex-direction: row;
      justify-content: flex-start;
      gap: 12px;
      max-width: 100%;
      border-right: none;
      border-bottom: 2px solid #DEB992;
      margin: 0 0 20px;
      padding: 0 0 20px;
      img {
        max-width: 60px;
      }
    }
  }
  &__title {
    font-weight: 600;
    font-size: 16px;
    margin-top: 15px;
    @include response(767px) {
      font-size: 22px;
      margin: 0;
    }
  }
  &__content {
    line-height: 175%;
  }
}

.best-mezcal {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px;
  background: #F9F9FA;
  border-radius: 5px;
  margin-bottom: 30px;
  &__thumb {
    flex-shrink: 0;
    margin-left: 20px;
    background: #FFFFFF;
    border: 4px solid #DEB992;
    border-radius: 5px;
    padding: 12px;
  }
  &__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    margin-bottom: 10px;
  }
  &__caption {
    font-size: 16px;
    line-height: 175%;
    margin-bottom: 30px;
  }
  &__info {
    display: flex;
    align-items: center;
    .review-rating {
      max-width: 170px;
    }
  }
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    height: 44px;
    padding: 0 10px;
    width: 144px;
    background: #DEB992;
    border-radius: 5px;
    text-decoration: none;
    font-weight: 500;
    color: #0B1014;
    @include transition();
    svg {
      margin-top: -2px;
      path {
        @include transition();
      }
    }
    &:hover {
      background: #1ba098;
      color: #fff;
      svg path {
        fill: #fff;
      }
    }
  }
}
.mezcal-brand {
  display: flex;
  justify-content: space-between;
  border-radius: 5px;
  overflow: hidden;
  border: 4px solid #F9F9FA;
  background: #fff;
  margin-bottom: 30px;
  &__thumb {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #F9F9FA;
    padding: 10px;
    flex-shrink: 0;
    max-width: 200px;
    width: 100%;
  }
  &__inner {
    padding: 30px;
  }
  &__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    margin-bottom: 10px;
  }
  &__content {
    line-height: 175%;
    margin-bottom: 15px;
  }
  &__link {
    display: flex;
    align-items: center;
    margin-left: auto;
    max-width: 105px;
    gap: 8px;
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: #1BA098;
    text-decoration: none;
    svg {
      margin-top: -2px;
    }
  }
}
.aging-item.affordable {
  position: relative;
  padding-left: 170px;
  .aging-item__left {
    padding: 0;
    position: absolute;
    left: 0;
    bottom: 0;
  }
  .aging-item__info-block {
    max-width: 100%;
    display: flex;
    align-items: center;
    gap: 5px;
  }
  .aging-item__info-block-title {
    margin-bottom: 0;
  }
  @include response(767px) {
    padding: 20px 10px;
    .aging-item__inner {
      flex-wrap: nowrap;
    }
    .aging-item__img {
      display: flex;
    }
    .aging-item__left {
      position: static;
    }
    .aging-item__info-block {
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
  }
}
.cocktail-item {
  border-radius: 5px;
  overflow: hidden;
  background: #F9F9FA;
  margin-bottom: 30px;
  &__inner {
    padding: 40px 30px;
  }
  &__title {
    font-family: 'Playfair Display', sans-serif;
    font-weight: 700;
    font-size: 26px;
    line-height: 125%;
    margin-bottom: 20px;
  }
  &__link {
    display: inline-flex;
    background: #DEB992;
    border-radius: 5px;
    align-items: center;
    gap: 10px;
    padding: 0 20px;
    height: 54px;
    text-decoration: none;
    color: #0B1014;
    font-weight: 500;
    font-size: 14px;
    @include transition();
    svg path {
      @include transition()
    }
    &:hover {
      color: #fff;
      background: #1ba098;
      svg path {
        fill: #fff;
      }
    }
  }
}