.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  box-shadow: 0px 5px 75px rgba(0, 0, 0, 0.075);
  &__inner {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
    justify-content: space-between;
    @extend %flex-v;
    @include response(1200px) {
      padding: 0 20px;
    }
    @include response(991px) {
      justify-content: space-between;
    }
  }
  &__top {
    background: $black;
    max-height: 35px;
    height: 35px;
    transition: .3s ease-in-out;
    @extend %flex-v;
    .header__inner {
      justify-content: flex-end;
    }
    @include response(767px) {
     display: none;
    }
  }
  &__top-nav {
    ul {
      @extend %list;
      @extend %flex-v;
      li {
        font-weight: 600;
        font-size: 12px;
        line-height: 12px;
        margin-right: 30px;
        a {
          color: #fff;
          text-decoration: none;
          transition: .3s ease-in-out;
          @include hover() {
            color: $brown;
          }
        }
      }
    }
  }
  &__top-controls {
    @extend %flex-v;
  }
  &__top-control {
    font-weight: 600;
    font-size: 12px;
    &.login {
      position: relative;
      margin-right: 24px;
      &::after {
        content: '';
        position: absolute;
        right: -12px;
        top: 50%;
        transform: translateY(-50%);
        width: 1px;
        height: 100%;
        background: #fff;
        opacity: .3;
      }
    }
    a {
      @extend %flex-v;
      font-weight: 400;
      font-size: 14px;
      color: #fff;
      text-decoration: none;
      transition: .3s ease-in-out;
      svg {
        fill: #fff;
        margin-right: 8px;
        margin-top: -2px;
        transition: .3s ease-in-out;
      }
      @include hover() {
        color: $brown;
        svg {
          fill: $brown;
        }
      }
    }
  }
  &__main {
    background: #fff;
    @extend %flex-v;
    height: 80px;
    @include response(767px) {
      height: 70px;
    }
  }
  &__menu-opener {
    display: none;
    @include response(991px) {
      display: block;
    }
  }
  &.fixed {
    .header__top {
      max-height: 0;
    }
  }
}

.main-nav {
  ul {
    @extend %list;
    @extend %flex-v;
    margin: 0 -22px;
    .with-child {
      display: flex;
      align-items: center;
      &:after {
        content: '';
        margin-left: 4px;
        margin-top: -2px;
        @include triangle(down, 4px, #5A5E62);
        position: static;
        @include transition();
      }
      &:hover {
        &:after {
          border-top-color: $green;
        }
        a {
          color: $green;
        }
        .submenu {
          display: block;
        }
      }
    }
    li {
      font-size: 14px;
      margin: 0 22px;
      a {
        color: $black;
        text-decoration: none;
        transition: .3s ease-in-out;
        @include transition();
        &:hover {
          color: #1ba098;
        }
      }
    }
  }
  @include response(991px) {
    display: none;
  }
}

.submenu {
  position: absolute;
  left: 0;
  top: calc(100% - 35px);
  padding: 35px 0 0;
  width: 100%;
  display: none;
  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 30px;
    width: 100%;
    height: 60px;
    background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(249,249,250,0) 53%);
    opacity: .07;
  }
  &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 40px;
    background-image: url("../data/menu-line.jpg");
  }
  &__inner {
    padding: 50px 15px 90px;
    background: #fff;
  }
  &__list {
    max-width: 1230px;
    margin: 0 auto !important;
    padding: 0;
    display: flex;
    gap: 15px;
     a {
       text-decoration: none;
       color: #0B1014 !important;
     }
    li {
      margin: 0 !important;
    }
  }
  &-item {
    width: 180px;
    display: flex;
    flex-direction: column;
    text-align: center;
    &__img {
      display: flex;
      justify-content: center;
      position: relative;
      margin-bottom: 30px;
      img {
        position: relative;
        z-index: 2;
      }
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 90px;
        background: #F9F9FA;
        border-radius: 8px;
        left: 0;
        top: 15px;
        @include transition();
      }
    }
    &__title {
      font-family: 'Playfair Display', sans-serif;
      font-weight: 700;
      font-size: 19px;
      max-width: 120px;
      line-height: 125%;
      margin: 0 auto;
    }
    &:hover {
      .submenu-item__img::after {
        background: #deb992;
      }
    }
  }
}

.logo {
  @include response(767px) {
    max-width: 135px;
  }
}

.search {
  width: 145px;
  display: flex;
  justify-content: flex-end;
  position: relative;
  @include response(991px) {
    margin: 0;
  }
  &__input {
    border: 1px solid #1BA098;
    border-radius: 5px;
    height: 40px;
    background: #fff;
    position: absolute;
    top: -5px;
    right: 0;
    z-index: 2;
    padding: 0 30px 0 25px;
    width: 0;
    visibility: hidden;
    @include transition();
  }
  &__close {
    position: absolute;
    top: 5px;
    right: 12px;
    z-index: 3;
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
    @include transition();
  }
  &__button {
    @extend %flex-c;
    width: 32px;
    height: 30px;
    border: 1px solid #5a5e62;
    border-radius: 5px;
    cursor: pointer;
    transition: .3s ease-in-out;
    @include hover() {
      background: $brown;
      border-color: $brown;
    }
  }
  &.active {
    .search__input {
      width: 390px;
      visibility: visible;
    }
    .search__close {
      opacity: 1;
      pointer-events: all;
    }
  }
}