.author {
  background: #f9f9fa;
  padding: 50px 0 40px;
  margin-bottom: 75px;
  @include response(767px) {
    padding: 30px 0 40px;
    margin-bottom: 50px;
    border-bottom: 1px solid #ebebee;
  }
  &__inner {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: flex-start;
    &--article {
      max-width: 1030px;
    }
    @include response(1200px) {
      padding: 0 20px;
    }
  }
  &__image {
    @extend %flex-c;
    width: 130px;
    height: 130px;
    flex-shrink: 0;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 50px;
    @include response(767px) {
      width: 80px;
      height: 80px;
      margin-right: 15px;
    }
  }
  &__content {
    max-width: 520px;
    width: 100%;
    flex-basis: 520px;
  }
  &__top {
    @extend %flex-v;
    gap: 15px;
    margin-bottom: 10px;
    @include response(767px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__name {
    font-weight: 600;
    font-size: 24px;
    @include response(767px) {
      font-size: 22px;
    }
  }
  &__options {
    @extend %flex-v;
    margin: 0 -2px 30px;
    @include response(767px) {
      margin-bottom: 20px;
    }
  }
  &__option {
    padding: 5px 10px;
    font-weight: 500;
    font-size: 12px;
    border-radius: 5px;
    margin: 0 2px !important;
    &.stories {
      background: $black;
      color: #fff;
    }
    &.reviews {
      background: $brown;
      color: $black;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 175%;
    @include response(767px) {
      margin-left: -90px;
    }
  }
  &__socials {
    @extend %list;
    @extend %flex-v;
    li {
      @extend %flex-c;
      width: 28px;
      height: 28px;
      border: 1px solid #deb992;
      background: #fff;
      border-radius: 50%;
      margin: 0 4px;
      transition: .3s ease-in-out;
      a {
        @extend %flex-c;
        width: 100%;
        height: 100%;
      }
      svg {
        fill: #5a5e62;
        transition: .3s ease-in-out;
      }
      @include hover() {
        background: #deb992;
        svg {
          fill: #fff;
        }
      }
    }
    @include response(767px) {
      margin: 0 -4px;
    }
  }
}

.stories {
  &__inner {
    max-width: 860px;
    width: 100%;
    margin: 0 auto 100px;
    @include response(991px) {
      padding: 0 20px;
    }
    @include response(767px) {
      margin: 0 auto 50px;
    }
    &.row-4 {
      max-width: 1160px;
      .stories__item {
        max-width: calc(25% - 40px);
        flex-basis: calc(25% - 40px);
        @include response(767px) {
          max-width: calc(50% - 40px);
          flex-basis: calc(50% - 40px);
        }
      }
    }
  }
  &__head-title {
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    margin-bottom: 65px;
    text-align: center;
    @include response(767px) {
      font-weight: 600;
      font-size: 22px;
      margin-bottom: 30px;
    }
  }
  &__list {
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    margin: 0 -20px 52px;
    @include response(767px) {
      margin: 0 -20px;
    }
  }
  &__item {
    max-width: calc(33.33333% - 40px);
    width: 100%;
    flex-basis: calc(33.33333% - 40px);
    margin: 0 20px 42px;
    @include response(767px) {
      max-width: calc(50% - 40px);
      flex-basis: calc(50% - 40px);
    }
    @include response(520px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__image {
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 8px;
    img {
      width: 100%;
      transition: .3s ease-in-out;
    }
    @include hover() {
      img {
        transform: scale(1.08);
      }
    }
  }
  &__date {
    @extend %flex-v;
    font-size: 12px;
    color: rgba(90, 94, 98, 0.8);
    margin-bottom: 6px;
    svg {
      margin-right: 7px;
      margin-top: -2px;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 175%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    a {
      color: $black;
      text-decoration: none;
    }
    @include response(520px) {
      font-size: 16px;
      font-weight: 400;
    }
  }
}

.subscribe-block {
  background: #f9f9fa;
  border-top: 1px solid #ebebee;
  padding: 75px 0;
  @include response(767px) {
    padding: 40px 20px;
  }
  &__inner {
    max-width: 560px;
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  &__title {
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 8px;
    @include response(767px) {
      font-size: 22px;
    }
  }
  &__text {
    font-weight: 500;
    font-size: 14px;
    margin-bottom: 30px;
  }
  &__form {
    @extend %flex-v;
    width: 100%;
    margin-bottom: 50px;
    @include response(580px) {
      flex-direction: column;
      margin-bottom: 30px;
    }
  }
  &__input {
    max-width: 380px;
    height: 55px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    padding: 0 16px;
    font-size: 16px;
    color: #5a5e62;
    &::placeholder {
      color: rgba(90, 94, 98, 0.5)
    }
  }
  &__button {
    @extend %flex-c;
    max-width: 170px;
    width: 100%;
    height: 55px;
    border: none;
    border-radius: 5px;
    background: $green;
    text-decoration: none;
    cursor: pointer;
    transition: .3s ease-in-out;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    margin-left: 10px;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: rgba(27, 160, 152, 0.75);
    }
    @include response(580px) {
      max-width: 100%;
      margin: 20px 0 0;
    }
  }
  &__info {
    @extend %flex-c;
    font-weight: 500;
    font-size: 14px;
    svg {
      margin-left: 7px;
    }
  }
}