.brand-wrapp {
  .bottle-tabs {
    padding: 0;
    margin: 0;
    &__list {
      margin: 0;
      padding: 0 15px;
    }
  }
  .bottle-tabs__container {
    background: #F9F9FA;
    padding-top: 80px;
  }
}
.brand-heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1190px;
  padding: 0 15px;
  margin: 0 auto 75px;
  @include response(992px) {
    justify-content: center;
  }
  &__img {
    flex-shrink: 0;
    @include response(1200px) {
      flex-shrink: 1;
      width: 100%;
      max-width: 50%;
    }
    @include response(992px) {
      display: none;
    }
  }
  &__logo {
    margin-bottom: 40px;
    @include response(992px) {
      text-align: center;
    }
  }
  &__title {
    font-family: 'Playfair Display', sans-serif;
    font-weight: 700;
    font-size: 54px;
    line-height: 125%;
    margin-bottom: 30px;
    @include response(767px) {
      font-size: 34px;
    }
  }
  &__caption {
    font-size: 16px;
    line-height: 175%;
    color: #5A5E62;
    margin-bottom: 24px;
  }
  &__info {
    display: flex;
    gap: 5px;
    margin-right: -50px;
    @include response(992px) {
      flex-wrap: wrap;
    }
    &-item {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 43px;
      padding: 0 15px;
      font-size: 14px;
      gap: 10px;
      background: #FFFFFF;
      border: 1px solid #EBEBEE;
      border-radius: 5px;
      @include response(992px) {
        flex-shrink: 0;
        padding: 0 5px;
      }
      a {
        text-decoration: none;
        color: #1ba098;
        font-weight: 500;
      }
      &.added-by {
        background: #F9F9FA;
      }
    }
  }
}
#bottles {
  padding: 60px 0;
  @include response(767px) {
    padding: 30px 0;
  }
}
.bottles {
  .bottle-tabs__container {
    padding: 0 15px;
  }
  &__title {
    margin-bottom: 60px;
    @include response(767px) {
      font-size: 22px;
      margin-bottom: 30px;
    }
  }
  &-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
    @include response(767px) {
      margin: 0 -15px;
    }
    .best-products__item {
      height: auto;
      padding: 40px 15px 0;
      display: flex;
      align-items: center;
      flex-direction: column;
      text-align: center;
      justify-content: space-between;
      @include response(767px) {
        max-width: calc(50% - 2px);
        margin: 0 1px 2px;
        border-radius: 0;
        padding: 40px 10px 0;
      }
    }
    .best-products__item-box {
      max-width: 100%;
    }
    .best-products__item-image {
      position: static;
    }
    .best-products__item-info {
      margin-bottom: 20px;
    }
  }
}
#our-review {
  background: #fff;
}
.our-review {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 60px 15px;
  gap: 20px;
  @include response(767px) {
    flex-direction: column;
    align-items: center;
  }
  &-aside {
    width: 320px;
    flex-shrink: 0;
    background: #F9F9FA;
    border-radius: 5px;
    padding: 30px;
    @include response(767px) {
      order: -1;
      max-width: 100%;
      width: 100%;
      margin-bottom: 30px;
    }
    .h5 {
      margin-bottom: 5px;
      line-height: 150%;
    }
  }
}
.bottles-count {
  display: flex;
  align-items: center;
  gap: 5px;
  font-weight: 600;
  font-size: 24px;
  &__label {
    margin-right: 9px;
    font-weight: 500;
    font-size: 14px;
  }
  svg {
    margin-top: -5px;
  }
}
.selling-list {
  padding: 0;
  list-style: none;
  margin: 15px 0 0;
  li {
    position: relative;
    padding-left: 31px;
    margin-bottom: 15px;
    font-size: 13px;
    &:last-child {
      margin: 0;
    }
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: -5px;
      width: 21px;
      height: 21px;
      background-image: url("../images/check.png");
      background-size: cover;
    }
  }
}
.similar-brand {
  background: #F9F9FA;
  padding: 80px 0;
  &.active {
    padding: 60px 0;
  }
  .bottle-tabs__container {
    padding: 0 15px;
  }
  &-title {
    margin-bottom: 40px;
  }
  &-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
    @include response(992px) {
      margin: 0 -10px;
    }
    @include response(767px) {
      margin: 0 -15px;
    }
  }
  &-item {
    width: calc(25% - 40px);
    background: #fff;
    text-align: center;
    text-decoration: none;
    color: #0B1014;
    margin: 0 20px 40px;
    border: 1px solid #EBEBEE;
    border-radius: 5px;
    &__title {
      display: flex;
      height: 64px;
      align-items: center;
      justify-content: center;
      font-family: 'Playfair Display', sans-serif;
      font-weight: 700;
      font-size: 19px;
      border-bottom: 1px solid #EBEBEE;
    }
    &__img {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 25px 20px;
      height: 105px;
    }
    @include response(992px) {
      margin: 0 10px 20px;
      width: calc(25% - 20px);
    }
    @include response(767px) {
      width: calc(50% - 2px);
      margin: 0 1px 2px;
      border-radius: 0;
    }
  }
}
