body {
  height: 100%;
  font-family: $Mfont;
  color: $black;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  &.custom-page {
    background: #f9f9fa;
  }
}
* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
svg {
  transform: translateZ(0);
}
input,
textarea {
  font-family: $Mfont;
  width: 100%;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type="submit"] {
  cursor: pointer;
}
h1,
.h1 {
  font-size: 32px;
  line-height: 150%;
  font-weight: 600;
}
h2,
.h2 {
  font-size: 27px;
  line-height: 150%;
  font-weight: 600;
}
h3,
.h3 {
  font-size: 24px;
  line-height: 24px;
  font-weight: 600;
}
h4,
.h4 {
  font-weight: 600;
  font-size: 22px;
  line-height: 150%;
}
h5,
.h5 {
  font-size: 20px;
  line-height: 150%;
  font-weight: 500;
}
h6,
.h6 {
  font-size: 18px;
  line-height: 150%;
  font-weight: 500;
}
p.error {
  font-size: 12px;
  line-height: 14px;
  color: red;
}
.wrapper {
  overflow: hidden;
  min-height: 100vh;
  position: relative;
  padding-top: 115px;
  @include response(767px) {
    padding-top: 70px;
  }
}
.holder-block {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.form-block {
  position: relative;
  width: 100%;
  margin-bottom: 15px;
  &__row {
    display: flex;
    align-items: center;
    margin: 0 -10px 15px;
    .form-block {
      margin: 0 10px;
    }
    @include response(767px) {
      flex-direction: column;
      margin: 0;
      .form-block {
        margin: 0 0 15px;
      }
    }
  }
}

.form-label {
  @extend %flex-v;
  font-weight: 500;
  font-size: 14px;
  color: #5a5e62;
  margin-bottom: 10px;
  span {
    color: $pink;
    margin-right: 5px;
  }
  small {
    margin-left: auto;
    font-size: 14px;
    color: rgba(90, 94, 98, 0.5);
    span {
      color: rgba(90, 94, 98, 0.5);
    }
  }
}

.main-input {
  width: 100%;
  height: 46px;
  background: #f9f9fa;
  border: 1px solid #ebebee;
  border-radius: 5px;
  padding: 0 15px;
  color: #5a5e62;
  font-size: 14px;
  &::placeholder {
    color: rgba(90, 94, 98, 0.5);
  }
  &:focus {
    background: #fff;
    border-color:#5a5e62;
  }
  &:active {
    background: #fff;
    border-color:#5a5e62;
  }
  &.error {
    border-color: $pink;
    color: $pink;
  }
}

.main-textarea {
  width: 100%;
  height: 165px;
  background: #f9f9fa;
  border: 1px solid #ebebee;
  border-radius: 5px;
  padding: 15px;
  color: #5a5e62;
  font-size: 14px;
  resize: none;
  overflow: hidden;
  &::placeholder {
    color: rgba(90, 94, 98, 0.5);
  }
  &:focus {
    border-color:#5a5e62;
  }
  &:active {
    border-color:#5a5e62;
  }
  &.error {
    border-color: $pink;
    color: $pink;
  }
  @include response(767px) {
    height: 120px;
  }
}

.submit-button {
  @extend %flex-c;
  max-width: 100%;
  width: 100%;
  height: 46px;
  background: $green;
  font-weight: 500;
  font-size: 14px;
  color: #fff;
  transition: .3s ease-in-out;
  text-decoration: none;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  svg {
    margin-left: 10px;
  }
  @include hover() {
    background: rgba(27, 160, 152, 0.75);
  }
  &--red {
    background: $pink;
    @include hover() {
      background: rgba(243, 28, 69, 0.75);
    }
  }
}

.checkbox-block {
  input[type=checkbox] {
    display: none;
  }
  input[type=checkbox] + span {
    display: block;
    position: relative;
    font-weight: 400;
    font-size: 14px;
    line-height: 165%;
    color: #5a5e62;
    padding: 0 0 0 28px;
    cursor: pointer;
    &::before {
      content: '';
      position: absolute;
      top: -2px;
      left: 0;
      width: 18px;
      height: 18px;
      background: transparent;
      border: 1px solid $green;
      border-radius: 5px;
    }
    &::after {
      content: '';
      position: absolute;
      top: -2px;
      left: 0;
      width: 18px;
      height: 18px;
      background-image: url('../data/check.svg');
      background-repeat: no-repeat;
      background-position: center;
      opacity: 0;
      overflow: hidden;
      visibility: hidden;
    }
  }
  input[type=checkbox]:checked + span {
    &::after {
      visibility: visible;
      opacity: 1;
      overflow: visible;
    }
  }
}

.form-error {
  padding: 15px;
  background: $pink;
  border-radius: 5px;
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 13px;
  color: #fff;
  a {
    color: #fff;
    text-decoration: none;
    font-weight: 600;
    @include transition();
    &:hover {
      color:#0B1014;
    }
  }
}

.form-info {
  font-weight: 400;
  font-size: 12px;
  color: #7e8084;
  margin-top: 5px;
  padding: 0 8px;
}

.main-button {
  @extend %flex-c;
  max-width: 230px;
  width: 100%;
  height: 46px;
  background: $green;
  font-weight: 500;
  font-size: 14px;
  color: #fff;
  transition: .3s ease-in-out;
  text-decoration: none;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  svg {
    margin-right: 10px;
  }
  @include hover() {
    background: rgba(27, 160, 152, 0.75);
  }
}

.pagination {
  @extend %flex-c;
  list-style: none;
  padding: 0;
  width: 100%;
  margin: 0 auto;
  @include response(600px) {
    justify-content: center;
  }
  li {
    margin: 0;
    a,
    span {
      @extend %flex-c;
      width: 36px;
      height: 36px;
      background: transparent;
      text-decoration: none;
      font-weight: 500;
      font-size: 14px;
      color: $black;
      margin: 0 2px;
      transition: .3s ease-in-out;
      @include response(1023px) {
        display: none;
      }
      @include hover() {
        color: $green;
      }
    }
    span {
      background: #f9f9fa;
      color: #5a5e62;
      @include response(1023px) {
        display: flex;
      }
      @include hover() {
        color: #5a5e62;
      }
    }
    a {
      color: $black;
    }
    &.first {
      position: relative;
      width: 28px;
      height: 28px;
      color: $black;
      @extend %flex-c;
      margin-right: 5px;
      border: 1px solid #ebebee;
      border-radius: 50%;
      a {
        background: transparent;
        width: 100%;
        height: 100%;
        margin: 0;
        transition: .3s ease-in-out;
        @extend %flex-c;
        @include response(500px) {
          display: flex;
        }
      }
      @include response(1023px) {
        a {
          display: flex;
        }
      }
      @include hover() {
        background: #f9f9fa;
      }
      &.disabled {
        opacity: 0.3;
        pointer-events: none;
      }
    }
    &.last {
      position: relative;
      width: 28px;
      height: 28px;
      color: $black;
      @extend %flex-c;
      margin-left: 5px;
      border: 1px solid #ebebee;
      border-radius: 50%;
      a {
        background: transparent;;
        width: 100%;
        height: 100%;
        margin: 0;
        transition: .3s ease-in-out;
        @extend %flex-c;
        @include response(500px) {
          display: flex;
        }
      }
      @include response(1023px) {
        a {
          display: flex;
        }
      }
      @include hover() {
        background: #f9f9fa;
      }
      &.disabled {
        opacity: 0.3;
        pointer-events: none;
      }
    }
    &.dots {
      @extend %flex-c;
      flex-shrink: 0;
      position: relative;
      width: 36px;
      height: 36px;
      text-decoration: none;
      font-size: 14px;
      font-weight: bold;
      &::before {
        content: '...';
        width: 16px;
        font-size: 16px;
        color: $black;
        text-align: center;
      }
      @include response(600px) {
        display: flex;
      }
    }
    &:nth-child(2) {
      @include response(1023px) {
        a {
          display: flex;
        }
      }
    }
    &:nth-last-child(2) {
      @include response(1023px) {
        a {
          display: flex;
        }
      }
    }
    @include response(600px) {
      &.active {
        & ~ li {
          &:not(.dots) {
            margin-left: 0;
          }
        }
      }
    }
  }
}

.mark {
  @extend %flex-v;
  margin-bottom: 25px;
  &__title {
    font-weight: 500;
    font-size: 14px;
    color: rgba(90, 94, 98, 0.9);
    margin-right: 26px;
  }
  &__rate {
    .rate {
      margin: 0 0 5px;
    }
    .rate__item {
      cursor: pointer;
      width: 30px;
      height: 28px;
    }
  }
}