.brands {
  &-wrapp {
    padding: 75px 15px;
    max-width: 1190px;
    margin: 0 auto;
    @include response(767px) {
      padding: 50px 15px;
    }
  }
  &-title {
    font-family: "Playfair Display", sans-serif;
    font-weight: 700;
    font-size: 54px;
    line-height: 125%;
    margin-bottom: 30px;
    @include response(767px) {
      font-size: 34px;
      margin-bottom: 20px;
    }

  }
  &-caption {
    font-size: 18px;
    line-height: 175%;
    margin-bottom: 75px;
    max-width: 430px;
    @include response(767px) {
      margin-bottom: 30px;
    }
  }
  &-heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    @include response(767px) {
      flex-wrap: wrap;
      gap: 15px;
    }
  }
  &-search {
    position: relative;
    @include response(992px) {
      max-width: 35%;
    }
    @include response(767px) {
      max-width: 100%;
      width: 100%;
    }
    &__field {
      border: 1px solid #ebebee;
      border-radius: 5px;
      height: 55px;
      max-width: 100%;
      width: 430px;
      padding: 0 45px 0 15px;
      background: #fff;
    }
    &__btn {
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid #5a5e62;
      border-radius: 5px;
      position: absolute;
      right: 12px;
      top: 12px;
      background: #fff;
      cursor: pointer;
      @include transition();
      svg > * {
        @include transition();
      }
      &:hover {
        background: #0b1014;
        svg > * {
          stroke: #fff;
        }
      }
    }
  }
}
.brands-filters {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  @include response(767px) {
    flex-wrap: wrap;
    width: 100%;
  }
  &__clear {
    font-weight: 500;
    font-size: 14px;
    color: #5a5e62;
    margin-right: 20px;
    cursor: pointer;
    @include response(767px) {
      order: 5;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
      margin-top: 10px;
    }
  }
  .select {
    height: 45px;
    width: 177px;
    background: #fff;
    display: flex;
    align-items: center;
    border-color: #5a5e62;
    font-size: 14px;
    @include response(767px) {
      width: calc(50% - 10px);
    }
    &__current.placeholder {
      color: #5a5e62;
    }
    &__list {
      padding: 7px 15px;
    }
    &--open {
      background: #1ba098;
      border-color: #1ba098;
      color: #fff;
      &::after {
        border-color: #fff;
      }
      .select__current.placeholder {
        color: #fff;
      }
    }
    &__item {
      padding: 5px 20px;
      border-bottom: 1px solid #ebebee;
      font-size: 16px;
      line-height: 175%;
      position: relative;
      color: #5a5e62;
      @include transition();
      &:last-child {
        border: none;
      }
      &::before {
        content: "";
        width: 12px;
        height: 12px;
        border-radius: 50%;
        border: 2px solid #ebebee;
        background: #ebebee;
        position: absolute;
        left: 0;
        top: 13px;
        @include transition();
      }
      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -1px;
        border-bottom: 3px solid #ebebee;
        width: 0;
        @include transition();
      }
      &:hover {
        background: transparent;
        color: #0b1014;
        &::after {
          width: 100%;
        }
        &::before {
          background: transparent;
        }
      }
      &--active {
        background: transparent;
        font-weight: 400;
        color: #0b1014;
        &::after {
          width: 100%;
          border-color: #1ba098;
        }
        &::before {
          border-color: #1ba098;
          background: transparent;
        }
      }
    }
  }
  .region-filter-wrapp {
    width: 177px;
    .select{
      display: none !important;
    }
    .selectMultiple {
      cursor: pointer;
      height: 45px;
      width: 100%;
      display: flex;
      align-items: center;
      font-size: 14px;
      border: 1px solid #5a5e62;
      border-radius: 5px;
      color: #5a5e62;
      position: relative;
      background-color: #fff;
      &::after {
        border-bottom: 1px solid #5a5e62;
        border-right: 1px solid #5a5e62;
        content: '';
        height: 7px;
        width: 7px;
        margin-top: -4px;
        pointer-events: none;
        position: absolute;
        right: 25px;
        top: 50%;
        transform-origin: 66% 66%;
        transform: rotate(45deg);
        transition: all 0.15s ease-in-out;
      }
      .placeholder {
        &.hide {
          display: none;
        }
      }
      & > div {
        width: 100%;
          position: relative;
          z-index: 2;
          padding: 13px 35px 13px 16px;
          border-radius: 5px;
          font-size: 14px;
          @include transition();
        span {
          pointer-events: none;
        }
        }
      .selected-text {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        display: block;
      }
      ul {
        position: absolute;
        @extend %list;
        width: 220px;
        left: 0;
        top: 100%;
        background-color: #fff;
        border: 1px solid #e8e8e8;
        box-sizing: border-box;
        margin-top: 2px;
        opacity: 0;
        padding: 15px;
        transform-origin: 50% 0;
        transform: scale(0.75) translateY(-2em);
        transition: transform 0.2s cubic-bezier(0.5, 0, 0, 1.25), opacity 0.1s ease-out;
        max-height: 200px;
        overflow: scroll;
        border-radius: 5px;
        li {
          font-size: 16px;
          line-height: 175%;
          color: #5A5E62;
          padding: 5px 20px;
          margin-bottom: 5px;
          border-bottom: 1px solid #EBEBEE;
          position: relative;
          &:last-child {
            border: none;
          }
          &::before {
            content: "";
            width: 12px;
            height: 12px;
            border-radius: 50%;
            border: 2px solid #ebebee;
            background: #ebebee;
            position: absolute;
            left: 0;
            top: 13px;
            @include transition();
          }
          &::after {
            content: "";
            position: absolute;
            left: 0;
            bottom: -1px;
            border-bottom: 3px solid #ebebee;
            width: 0;
            @include transition();
          }
          &:hover {
            background: transparent;
            color: #0b1014;
            &::after {
              width: 100%;
            }
            &::before {
              background: transparent;
            }
          }
          &.selected {
            background: transparent;
            font-weight: 400;
            color: #0b1014;
            &::after {
              width: 100%;
              border-color: #1ba098;
            }
            &::before {
              border-color: #1ba098;
              background: transparent;
            }
          }
        }
      }
      &.open {
        background: #1ba098;
        border-color: #1ba098;
        color: #fff;
        &::after {
          transform: rotate(-135deg);
          border-color: #fff;
        }
        ul {
          visibility: visible;
          opacity: 1;
          z-index: 1;
          transform: scale(1) translateY(0);
        }
      }
    }
  }
}
.brands-list {
  margin-bottom: 60px;
  @include response(767px) {
    margin-bottom: 40px;
  }
  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 45px;
    background: #0b1014;
    color: #fff;
    border-radius: 5px 5px 0 0;
    overflow: hidden;
    padding: 0 45px;
    gap: 10px;
    @include response(992px) {
      padding: 0 20px;
    }
    @include response(767px) {
      justify-content: flex-start;
      gap: 5px;
      font-size: 14px;
      .brands-list__cell {
        display: flex;
        &::after {
          content: '/';
          margin-left: 5px;
        }
        &:first-child,
        &:last-child {
          &::after {
            display: none;
          }
        }
      }
      .brand-cell {
        margin-right: 85px;
      }
    }
  }
  &__cell {
    &.brand-cell {
      width: 40%;
    }
    &.region-cell {
      width: 24%;
      @include response(992px) {
        width: 22%;
      }
    }
    &.variations-cell {
      width: 24%;
      @include response(992px) {
        width: 22%;
      }
    }
    &.rating-cell {
      width: 12%;
      @include response(992px) {
        width: 16%;
      }
    }
    @include response(767px) {
      width: auto !important;
      gap: 0 !important;
    }
  }
  &__inner {
    border: 1px solid #ebebee;
    border-radius: 0 0 5px 5px;
  }
}
.brands-list-item {
  display: flex;
  width: 100%;
  padding: 0 45px;
  text-decoration: none;
  color: #0b1014;
  position: relative;
  border-bottom: 1px solid #ebebee;
  @include transition();
  &:last-child {
    border-radius: 0 0 5px 5px;
    border: none;
  }
  &:hover {
    background: rgba(235, 235, 238, 0.5);
  }
  @include response(992px) {
    padding: 0 20px;
  }
  @include response(767px) {
    padding: 15px 20px;
    gap: 10px;
  }
  &.featured {
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border: 1px solid #deb992;
      pointer-events: none;
    }
  }
  .featured__icon {
    position: absolute;
    right: -25px;
    top: 0;
    height: 100%;
    width: 25px;
    background: #deb992;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 5px 5px 0;
    @include response(992px) {
      right: 0;
      height: 25px;
      width: 25px !important;
      border-radius: 0 0 0 5px;
    }
  }
  &__left {
    display: flex;
    width: 100%;
    gap: 10px;
    justify-content: space-between;
    @include response(767px) {
      width: 120px;
      flex-shrink: 0;
      gap: 0;
    }
  }
  &__right {
    display: flex;
    flex-wrap: wrap;
  }
  .brands-list__cell {
    height: 80px;
    display: flex;
    align-items: center;
  }
  &__img {
    position: relative;
    left: -45px;
    @include response(992px) {
      max-width: 120px;
      left: -20px;
    }
  }
  &__title {
    font-family: "Playfair Display", sans-serif;
    font-weight: 700;
    font-size: 17px;
    @include response(992px) {
      font-size: 16px;
      margin-left: -20px;
    }
    @include response(767px) {
      width: 100%;
      margin-left: 0;
    }
  }
  &__region {
    color: #5a5e62;
    @include response(767px) {
      width: 100%;
    }
  }
  &__variations,
  &__rating {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 600;
    font-size: 15px;
    &.empty {
      font-weight: 400;
      color: #5A5E62;
      svg path {
        stroke: #5A5E62;
        &:last-child  {
          stroke: transparent;
          fill: #5A5E62;
        }
      }
    }
    @include response(992px) {
      font-size: 13px;
    }
    @include response(767px) {
      width: 50%;
    }
  }
}
.brands-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  .pagination {
   margin-right: -170px;
    @include response(767px) {
      margin: 0;
    }
  }
}
.brands-pagination-count {
  flex-shrink: 0;
  margin-left: auto;
  color: #5A5E62;
  @include response(767px) {
    display: none;
  }
}
