.breadcrumbs {
  max-width: 1190px;
  padding: 30px 15px 15px;
  margin: 0 auto;
  &-list {
    display: flex;
    align-items: center;
    font-size: 14px;
    list-style: none;
    margin: 0 0 30px;
    padding: 0;
    @include response(767px) {
      flex-wrap: wrap;
    }
    li {
      display: flex;
      &::after {
        content: '/';
        margin: 0 12px;
      }
      &:last-child::after {
        display: none;
      }
    }
    a {
      color: #0b1014;
      text-decoration: none;
    }
    span {
      color: rgba(#0b1014, .4);
    }
  }
}