.main-section {
  width: 100%;
  height: calc(100vh - 115px);
  background: #f9f9fa;
  position: relative;
  overflow: hidden;
  @extend %flex-v;
  margin-bottom: 150px;
  @include response(767px) {
    margin-bottom: 70px;
    height: 600px;
  }
  &__inner {
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 2;
    @include response(991px) {
      padding: 0 20px;
    }
    @include response(767px) {
      padding: 0 10px;
    }
  }
  &__title {
    max-width: 560px;
    width: 100%;
    font-family: $Tfont;
    font-weight: 700;
    font-size: 54px;
    line-height: 125%;
    text-align: center;
    margin: 0 auto 75px;
    @include response(991px) {
      font-size: 44px;
      margin: 0 auto 50px;
    }
    @include response(767px) {
      font-size: 34px;
      margin: 0 auto 20px;
    }
  }
  &__description {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    margin-bottom: 45px;
    @include response(767px) {
      font-size: 18px;
      margin-bottom: 30px;
    }
  }
  &__box {
    width: 100%;
    background: #fff;
    border: 1px solid #ebebee;
    box-shadow: 0px 5px 75px rgba(0, 0, 0, 0.075);
    border-radius: 5px;
    padding: 40px 75px;
    @include response(991px) {
      padding: 30px 40px;
    }
    @include response(767px) {
      padding: 20px 10px;
    }
  }
  &__search {
    position: relative;
    margin-bottom: 25px;
    // @include response(580px) {
    //   flex-direction: column;
    // }
  }
  &__search-input {
    padding: 0 140px 0 25px;
    width: 100%;
    height: 75px;
    background: #ebebee;
    border-radius: 5px;
    font-size: 18px;
    border: none;
    &::placeholder {
      color: #5a5e62;
    }
    @include response(767px) {
      font-size: 16px;
    }
    @include response(580px) {
      height: 60px;
      font-size: 14px;
      padding: 0 20px;
      padding-right: 100px;
      &::placeholder {
        font-size: 12px;
      }
    }
  }
  &__search-submit {
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    max-width: 110px;
    width: 100%;
    height: 46px;
    @extend %flex-c;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    background: $green;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    transition: .3s ease-in-out;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: $black;
    }
    @include response(580px) {
      right: 7px;
      height: 30px;
      max-width: 80px;
      svg {
        display: none;
      }
    }
  }
  &__tags {
    overflow: hidden;
    @extend %flex-v;
    @include response(991px) {
      justify-content: center;
    }
    @include response(767px) {
      flex-direction: column; 
      align-items: flex-start;
    }
  }
  &__tags-title {
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: $gray;
    margin-right: 26px;
    @include response(767px) {
      display: none;
    }
  }
  &__tags-list {
    @extend %list;
    max-width: 590px;
    margin: 0 -7px;
    .slick-list {
      padding: 10px 0 !important;
    }
    .slick-prev.slick-arrow {
      display: none !important;
    }
    li {
      font-weight: 500;
      font-size: 14px;
      margin: 0 7px;
      a {
        color: $black;
        text-decoration: none;
        padding-bottom: 5px;
        border-bottom: 2px solid #ebebee;
        transition: .3s ease-in-out;
        @include hover() {
          color: $green;
          border-color: $green;
        }
      }
    }
    @include response(991px) {
      max-width: 450px; 
    }
    @include response(767px) {
      //max-width: 300px;
      padding: 0 10px;
    }
  }
  &__tags-arrow {
    position: absolute;
    right: -20px;
    top: 50%;
    transform: translateY(-50%);
    @include response(767px) {
      right: -15px;
    }
  }
}

.main-content {
  max-width: 1160px;
  width: 100%;
  margin: 0 auto 150px;
  @extend %flex-v;
  justify-content: space-between;
  @include response(1200px) {
    padding: 0 20px;
  }
  @include response(767px) {
    flex-direction: column;
    align-items: center;
    margin: 0 auto 50px;
  }
  &__image {
    max-width: 560px;
    width: 100%;
    flex-basis: 560px;
    text-align: center;
    margin: 0 40px 0 0;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
      margin: 0 auto 40px;
    }
  }
  &__content {
    max-width: 430px;
    width: 100%;
    flex-basis: 430px;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
      margin: 0 auto;
    }
  }
  &__title {
    font-family: $Tfont;
    font-weight: 700;
    font-size: 32px;
    margin-bottom: 30px;
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    p {
      @include marginY()
    }
  }
  &--reverse {
    flex-direction: row-reverse;
    .main-content__image {
      margin: 0 0 0 40px;
    }
    @include response(767px) {
      flex-direction: column;
      align-items: center;
      margin: 0 auto 50px;
      .main-content__image {
        max-width: 100%;
        flex-basis: auto;
        margin: 0 auto 40px;
      }
    }
  }
}

.holder-block {
  width: 100%;
  height: 38px;
  background-size: cover;
  background-repeat: no-repeat;
}

.brands-info {
  max-width: 1160px;
  width: 100%;
  margin: 0 auto;
  padding: 100px 0 90px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  @include response(1200px) {
    padding: 100px 20px 90px;
  }
  @include response(1023px) {
    flex-direction: column;
    align-items: center;
  }
  @include response(767px) {
    padding: 60px 20px 40px;
  }
  &__title {
    max-width: 350px;
    width: 100%;
    flex-basis: 350px;
    font-weight: 600;
    font-size: 32px;
    margin-right: 80px;
    @include response(1200px) {
      margin-right: 40px;
      font-size: 28px;
    }
    @include response(1023px) {
      max-width: 100%;
      flex-basis: auto;
      margin: 0 auto 30px;
    }
  }
  &__text {
    max-width: 730px;
    width: 100%;
    flex-basis: 730px;
    font-size: 16px;
    line-height: 175%;
    span {
      color: $green;
    }
    @include response(1023px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
}

.brands {
  max-width: 1240px;
  width: 100%;
  margin: 0 auto 30px;
  @include response(1400px) {
    padding: 0 50px;
  }
  @include response(767px) {
    padding: 0 20px;
  }
  &__item {
    display: flex;
    align-items: stretch;
    width: 100%;
    height: 360px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 0px 5px 5px 5px;
    margin-bottom: 20px;
    transition: .3s ease-in-out;
    &.special {
      border-color: $brown;
      .brands__item-number {
        background: $brown;
      }
      .brands__item-star {
        visibility: visible;
        opacity: 1;
        overflow: visible;
      }
    }
    @include response(767px) {
      flex-direction: column;
      align-items: center;
      height: auto;
    }
  }
  &__item-left {
    position: relative;
    max-width: 300px;
    width: 100%;
    flex-basis: 300px;
    @include response(991px) {
     flex-shrink: 0;
     max-width: 240px;
     flex-basis: 240px;
    } 
    @include response(767px) {
      flex-shrink: 1;
      max-width: 100%;
      flex-basis: auto;
      height: 360px;
    }
  }
  &__item-middle {
    max-width: 640px;
    width: 100%;
    flex-basis: 640px;
    padding: 50px 40px;
    border-left: 1px solid  #ebebee;
    border-right: 1px solid  #ebebee;
    @include response(991px) {
      padding: 30px 10px;
    } 
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__item-right {
    position: relative;
    max-width: 300px;
    width: 100%;
    flex-basis: 300px;
    background: #f9f9fa;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include response(991px) {
      flex-shrink: 0;
      max-width: 240px;
      flex-basis: 240px;
    }
    @include response(767px) {
      flex-shrink: 1;
      max-width: 100%;
      flex-basis: auto;
      height: 360px;
    } 
  }
  &__item-text {
    font-size: 15px;
    line-height: 175%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 10;
    p {
      @include marginY()
    }
    @include response(991px) {
      -webkit-line-clamp: 12;
    } 
    @include response(767px) {
      -webkit-line-clamp: 99;
    }
  }
  &__item-title {
    position: relative;
    @extend %flex-c;
    width: 100%;
    height: 130px;
    background: #f9f9fa;
    font-weight: 600;
    font-size: 24px;
    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      height: 4px;
      width: 100px;
      bottom: 0;
      background: $gray;
    }
  }
  &__item-image {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding-bottom: 50px;
  }
  &__item-number {
    @extend %flex-c;
    position: absolute;
    background: $black;
    width: 42px;
    height: 42px;
    right: 100%;
    top: 0;
    border-radius: 5px 0px 0px 5px;
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    color: #fff;
    transition: .3s ease-in-out;
    @include response(767px) {
      display: none;
    }
  }
  &__item-count {
    font-weight: 500;
    font-size: 14px;
    margin-bottom: 65px;
    span {
      font-weight: 600;
      font-size: 24px;
      vertical-align: middle;
      margin-left: 10px;
    }
    small {
      font-weight: 500;
      font-size: 18px;
      margin-left: 15px;
      vertical-align: super;
    }
  }
  &__item-rate {
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 5px;
  }
  &__item-rate-info {
    font-size: 14px;
    line-height: 14px;
    text-align: center;
    color: $gray;
    margin-bottom: 28px;
  }
  &__item-button {
    @extend %flex-c;
    max-width: 185px;
    width: 100%;
    height: 44px;
    background: $black;
    border-radius: 5px;
    font-size: 14px;
    color: #fff;
    text-decoration: none;
    border: none;
    cursor: pointer;
    transition: .3s ease-in-out;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: rgba(11, 16, 20, 0.7)
    }
  }
  &__item-star {
    position: absolute;
    right: 0;
    top: 0;
    @extend %flex-c;
    width: 50px;
    height: 50px;
    background: rgba(222, 185, 146, 0.5);
    border-radius: 0px 0px 0px 5px;
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
    transition: .3s ease-in-out;
  }
}

.rate {
  position: relative;
  display: flex;
  margin-bottom: 15px;
  &__item {
    flex: 0 0 auto;
    margin-right: 5px;
    fill: rgba(90, 94, 98, 0.4);
    [data-rate='1'] & {
      &:nth-child(1) {
        fill: $green;
      }
    }
    [data-rate='2'] & {
      &:nth-child(1),
      &:nth-child(2) {
        fill: $green;
      }
    }
    [data-rate='3'] & {
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3) {
        fill: $green;
      }
    }
    [data-rate='4'] & {
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4) {
        fill: $green;
      }
    }
    [data-rate='5'] & {
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5) {
        fill: $green;
      }
    }
  }
}

