    @include response(767px) {
      -webkit-line-clamp: 99;
      margin-bottom: 30px;
    }
  }
  &__link {
    padding: 0 25px;
    height: 44px;
    border-radius: 5px;
    background: $brown;
    @extend %flex-c;
    font-weight: 500;
    font-size: 14px;
    color: $black;
    cursor: pointer;
    text-decoration: none;
    transition: .3s ease-in-out;
    margin: auto 0 0;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: #e6cbad;
    }
    @include response(767px) {
      margin: 0 auto;
      padding: 0 15px;
      font-size: 13px;
    }
  }
  &__icon {
    text-align: center;
    padding-top: 20px;
  }
  &__picture {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
  }
}

.brand-list {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
  margin: 0 -20px;
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 35px 25px 25px;
    background: $black;
    border-radius: 5px;
    max-width: calc(33.33333% - 40px);
    width: 100%;
    flex-basis: calc(33.33333% - 40px);
    margin: 0 20px;
    @include response(991px) {
      max-width: calc(50% - 40px);
      flex-basis: calc(50% - 40px);
      margin: 0 20px 40px;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    @include response(580px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__title {
    font-family: $Tfont;
    font-weight: 700;
    font-size: 19px;
    color: #fff;
    margin-bottom: 55px;
  }
  &__image {
    position: relative;
    background: #fff;
    border: 4px solid $brown;
    border-radius: 5px;
    width: 100%;
    height: 310px;
    @include response(991px) {
      max-width: 210px;
    }
    @include response(580px) {
      max-width: 100%;
      text-align: center;
    }
  }
  &__icon {
    text-align: center;
    padding-top: 25px;
  }
  &__picture {
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
  }
}

.cocktails-block {
  background: $brown;
  padding: 60px 0 100px;
  @include response(1200px) {
    padding: 60px 20px 100px;
  }
  @include response(767px) {
    padding: 60px 20px 50px;
  }
  &__inner {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
  }
  &__box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 85px;
    @include response(991px) {
      flex-direction: column;
    }
    @include response(767px) {
      margin-bottom: 50px;
    }
  }
  &__left {
    max-width: 430px;
    width: 100%;
    flex-basis: 430px;
    margin-right: 40px;
    @include response(991px) {
      max-width: 100%;
      flex-basis: auto;
      margin: 0 auto 40px;
    }
  }
  &__right {
    max-width: 560px;
    width: 100%;
    flex-basis: 560px;
    @include response(991px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 32px;
    margin-bottom: 40px;
    @include response(767px) {
      font-size: 22px;
      margin-bottom: 30px;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 175%;
    p {
      @include marginY()
    }
    &--small {
      font-size: 16px;
    }
  }
  &__image {
    width: 100%;
    text-align: center;
    margin-bottom: 32px;
  }
}
.main-section-bg {
  position: absolute;
  left: -100px;
  top: 0;
  right: -100px;
  height: 200%;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 200px;
    background: rgb(249,249,250);
    background: linear-gradient(180deg, rgba(249,249,250,1) 49%, rgba(249,249,250,0.8702074579831933) 76%, rgba(249,249,250,0) 100%);
    display: none;
    @include response(767px) {
      display: block;
    }
  }
  @include response(767px) {
    left: 0;
    right: 0;
    top: 200px;
  }
  &__box {
    display: flex;
    align-items: center;
    gap: 20px;
    transform: rotate(-15deg);
    position: relative;
    @include response(767px) {
      &:last-child {
        display: none;
      }
    }
    &.right {
      right: -200px;
    }
  }
  &__line {
    width: 220px;
    height: 2100px;
    background-size: contain;
    @include response(767px) {
      width: 150px;
    }
