.article-heading {
  max-width: 680px;
  margin: 0 auto 30px;
  &__title {
    font-family: 'Playfair Display', sans-serif;
    font-weight: 700;
    font-size: 32px;
    line-height: 150%;
    margin-bottom: 20px;
  }
  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .learning-heading__info,
  .learning-heading__socials {
    margin: 0;
  }
}
.article {
  .learning-holder {
    max-width: 1060px;
  }
  .learning-aside {
    max-width: 300px;
    padding: 0;
  }
  .learning-content {
    max-width: 680px;
  }
}
.aside-item {
  background: #F9F9FA;
  border: 1px solid #DEB992;
  border-radius: 5px;
  padding: 30px;
  margin-bottom: 50px;
  @include response(767px) {
    max-width: 300px;
    margin: 0 auto 50px;
  }
  &__title {
    font-weight: 500;
    font-size: 15px;
    line-height: 175%;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    gap: 10px;
  }
}
.recent-stories {
  @include response(992px) {
    display: none;
  }
  &-item {
    display: flex;
    align-items: center;
    gap: 15px;
    color: #0B1014;
    text-decoration: none;
    margin-bottom: 25px;
    &:last-child {
      margin-bottom: 0;
    }
    &__thumb {
      border-radius: 5px;
      overflow: hidden;
      flex-shrink: 0;
    }
    &__title {
      font-weight: 500;
      font-size: 14px;
      line-height: 150%;
      margin-bottom: 7px;
    }
    &__date {
      display: inline-flex;
      align-items: center;
      gap: 7px;
      color: #5A5E62;
      opacity: 0.8;
      font-size: 12px;
      line-height: 12px;
      svg {
        margin-top: -2px;
      }
    }
  }
}
.inst-list {
  margin: 0 -5px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  a {
    max-width: calc(50% - 10px);

    margin: 0 5px 10px;
  }
  @include response(767px) {
    margin: 0 auto;
  }
}

.regulation-table {
  border-radius: 5px;
  border: 1px solid #EBEBEE;
  overflow: hidden;
  table {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
    td {
      padding: 19px 10px;
      border-bottom: 1px solid #EBEBEE;
    }
    tr:last-child td {
      border: none;
    }
    thead {
      td {
        background: #EBEBEE;
        font-weight: 600;
        font-size: 15px;
      }
    }
  }
}
.prons-cons {
  border: 1px solid #EBEBEE;
  margin-bottom: 30px;
  display: flex;
  @include response(767px) {
    flex-wrap: wrap;
  }
  &__head {
    display: flex;
    justify-content: space-between;
  }
  &__title {
    text-align: center;
    font-weight: 600;
    font-size: 15px;
    line-height: 175%;
    padding: 20px;
    border-bottom: 1px solid #EBEBEE;
  }
  &__inner {
    padding: 30px;
  }
  &__col {
    width: 50%;
    &:first-child {
      border-right: 1px solid #EBEBEE;
    }
    @include response(767px) {
      width: 100%;
      border-bottom: 1px solid #EBEBEE;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 20px;
    svg {
      flex-shrink: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.cols-block {
  display: flex;
  justify-content: space-between;
  @include response(767px) {
    flex-wrap: wrap;
  }
  &__col {
    width: 48%;
    @include response(767px) {
      width: 100%;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 20px;
  }
}
