.bottle-head-wrapper {
  background: #f9f9fa;
  padding: 0 15px 50px;
  .breadcrumbs {
    padding: 15px 0;
  }
}
.bottle-heading {
  max-width: 1160px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 50px;
  @include response(1100px) {
    justify-content: center;
  }
  @include response(767px) {
    align-items: flex-start;
    flex-direction: column;
    gap: 30px;
  }
  &__inner {
    width: 100%;
    max-width: 380px;
  }
  &__title {
    font-family: 'Playfair Display', serif;
    font-weight: 700;
    font-size: 40px;
    margin-bottom: 20px;
  }
  &__top {
    margin-bottom: 40px;
    @include response(768px) {
      margin-bottom: 0;
    }
  }
  &__aside {
    @include response(1100px) {
      display: none;
    }
  }
  &__info {
    width: 100%;
    max-width: 300px;
    padding: 30px;
    margin-bottom: 30px;
    background: #0b1014;
    border-radius: 5px;
    color: #fff;
    &-top {
      display: flex;
      align-items: center;
      gap: 15px;
      margin-bottom: 20px;
    }
    &-img {
      border-radius: 5px;
      overflow: hidden;
    }
    &-title {
      font-family: 'Playfair Display', serif;
      font-weight: 700;
      font-size: 16px;
    }
    &-content {
      font-size: 14px;
      line-height: 175%;
      margin-bottom: 20px;
    }
    &-link {
      font-weight: 500;
      font-size: 14px;
      text-decoration: none;
      color: #deb992;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    &-label {
      font-size: 14px;
      color: #5A5E62;
      margin-right: 5px;
    }
    &-link {
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      border: 1px solid #EBEBEE;
      @include transition();
      svg path {
        @include transition();
      }
      &:hover {
        background: #5A5E62;
        svg path {
          fill: #deb992;
        }
      }
    }
  }
}

.specification {
  width: 100%;
  td {
    padding: 10px 0;
    border-bottom: 1px solid #ebebee;
  }
  tr td:first-child {
    color: #5a5e62;
  }
}

.rating {
  &__inner {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
  }
  &__stars {
    display: flex;
    gap: 3px;
  }
  &__count {
    font-weight: 600;
    font-size: 18px;
  }
  &__reviews {
    font-size: 14px;
    color: #5a5e62;
    display: inline-flex;
    gap: 10px;
    align-items: center;
    text-decoration: none;
    svg {
      path,
      circle {
        @include transition();
      }
      &:hover {
        path,
        circle {
         stroke: #1ba098;
        }
      }
    }
  }
}

.bottle-container {
  max-width: 1190px;
  padding: 0 15px;
  margin: 0 auto;
}

.bottle-tabs {
  padding: 50px 0;
  margin-bottom: 51px;
  &__container {
    &.fixed-list {
      padding-top: 120px;
    }
  }
  &__holder {
    &.fixed {
      background: #fff;
      padding: 15px;
      position: fixed;
      top: 80px;
      left: 0;
      width: 100%;
      box-shadow: 0px 5px 75px rgba(0, 0, 0, 0.075);
      border-top: 1px solid #f9f9fa;
      z-index: 5;
      .bottle-tabs__list {
        max-width: 1160px;
        margin: 0 auto;
      }
      &.dark {
        background: #0B1014;
        .bottle-tabs__list a {
          color: #fff;
          &.active,
          &:hover {
            color: #1ba098;
          }
        }
      }
    }
  }
  &__list {
    list-style: none;
    padding: 0;
    margin: 0 0 99px;
    display: flex;
    align-items: center;
    gap: 25px;
    a {
      text-decoration: none;
      font-weight: 500;
      font-size: 16px;
      color: #0b1014;
      position: relative;
      padding-bottom: 5px;
      box-shadow: 0 2px 0 #EBEBEE;
      @include transition();
      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -2px;
        border-bottom: 4px solid #1ba098;
        width: 0;
        @include transition();
      }
      &:hover,
      &.active {
        color: #1ba098;
      }
      &.active {
        &::after {
          width: 100%;
        }
      }
    }
  }
  .tab {
    height: 0;
    overflow: hidden;
    opacity: 0;
    &.active {
      height: auto;
      opacity: 1;
    }
  }
}
.feedback {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 40px;
  @include response(767px) {
    flex-direction: column;
    align-items: center;
    gap: 15px;
  }
  &-inner {
    max-width: 730px;
    width: 100%;
  }
  &-aside {
    max-width: 320px;
    width: 100%;
    background: #F9F9FA;
    border-radius: 5px;
    padding: 30px;
    &__title {
      margin: 0 0 25px;
    }
    &__item {
      padding-bottom: 25px;
      margin-bottom: 25px;
      border-bottom: 1px solid #EBEBEE;
      &:last-child {
        padding: 0;
        margin: 0;
        border: none;
      }
    }
  }
  &-title {
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 32px;
    gap: 15px;
    margin-bottom: 32px;
    @include response(992px) {
      font-size: 26px;
    }
    @include response(767px) {
      font-size: 22px;
    }
    &__icon {
      width: 42px;
      height: 42px;
      display: flex;
      flex-shrink: 0;
      align-items: center;
      justify-content: center;
      background: #1ba098;
      border-radius: 50%;
    }
  }
  &__content {
    font-size: 16px;
    line-height: 175%;
    p {
      margin: 0 0 30px;
    }
    ul {
      list-style-type: none;
      padding: 0;
      margin: 0 0 30px;
      li {
        position: relative;
        padding-left: 31px;
        margin-bottom: 15px;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 21px;
          height: 21px;
          background-image: url("../images/check.png");
          background-size: cover;
        }
      }
    }
  }
}
.where-buy {
  &__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  &-item {
    display: flex;
    align-items: center;
    padding: 15px 30px 8px 15px;
    border-radius: 5px;
    border: 1px solid #EBEBEE;
    background: #fff;
    position: relative;
    text-decoration: none;
    color: #0b1014;
    @include transition();
    &__img {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 20px;
    }
    &__label {
      font-size: 14px;
      line-height: 14px;
      color: #5A5E62;
      opacity: .66;
      margin-bottom: 9px;
    }
    &__link {
      font-size: 18px;
      line-height: 18px;
    }
    &__icon {
      position: absolute;
      top: 12px;
      right: 12px;
      opacity: 0;
      @include transition();
    }
    &:hover {
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      border-color: transparent;
      color: #1ba098;
      .where-buy-item__icon {
        opacity: 1;
      }
    }
  }
}
.alternatives {
  .h4 {
    margin-bottom: 0;
  }
  &__heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
  }
  &__nav {
    display: flex;
    align-items: center;
    gap: 5px;
  }
  &__arrow {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    border: 1px solid #EBEBEE;
    cursor: pointer;
    svg {
      opacity: 0.3;
      @include transition();
    }
    &:hover svg {
      opacity: 1;
    }
  }
  .best-products__item {
    display: block;
    max-width: 100%;
    flex-basis: 100%;
    position: relative;
    margin: 0;
  }
}
#reviews {
  padding: 60px 0 20px;
  margin-top: 40px;
  border-top: 1px solid #EBEBEE;
}
#write-review, #more {
  border-top: 1px solid #EBEBEE;
  padding: 60px 0 0;
}
#more {
  margin-top: 60px;
}
.reviews {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  margin-bottom: 80px;
  @include response(992px) {
    flex-direction: column-reverse;
    align-items: center;
  }
  &__title {
    margin-bottom: 50px;
  }
  &-empty {
    display: flex;
    align-items: center;
    gap: 15px;
    font-size: 18px;
    color: #5A5E62;
    svg {
      margin-bottom: 5px;
    }
  }
  &-inner {
    max-width: 730px;
    width: 100%;
  }
  &-aside {
    width: 100%;
    max-width: 320px;
    @include response(767px) {
      max-width: 100%;
    }
    &__label {
      text-align: center;
      margin-bottom: 15px;
    }
  }
  &__footer {
    display: flex;
    align-items: center;
    justify-content: center;
    .pagination {
      margin: 0;
      width: auto;
    }
  }
  &__show-more {
    width: 160px;
    height: 46px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
    font-size: 15px;
    background: #fff;
    border: 1px solid #E6E6E6;
    border-radius: 5px;
    cursor: pointer;
    @include transition();
    &:hover {
      background: #0b1014;
      color: #fff;
    }
  }
}
.review {
  padding-bottom: 35px;
  margin-bottom: 30px;
  border-bottom: 1px solid #EBEBEE;
  &:last-child {
    border: none;
  }
  &__heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    .rate {
      margin: 0;
    }
  }
  &__meta,
  &__info {
    display: flex;
    align-items: center;
  }
  &__meta {
    gap: 19px;
  }
  &__name {
    font-size: 18px;
    font-weight: 600;
    line-height: 175%;
    @include response(767px) {
      line-height: 18px;
      margin-bottom: 8px;
      font-size: 16px;
    }
  }
  &__count {
    font-weight: 400;
    font-size: 16px;
    color: #5A5E62;
    @include response(767px) {
      margin-bottom: 10px;
    }
  }
  &__info {
    gap: 30px;
  }
  &-rating {
    gap: 10px;
    display: flex;
    font-weight: 600;
    font-size: 18px;
    flex-wrap: wrap;
    &-label {
      width: 100%;
      font-weight: 400;
      font-size: 13px;
      color: #5A5E62;
    }
    .rate {
      margin: 0;
    }
  }
  &__title {
    margin-bottom: 20px;
  }
  &__content {
    line-height: 175%;
    font-size: 16px;
    opacity: .9;
    p {
      margin: 0 0 30px;
    }
  }
  &-rate {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__label {
      font-size: 15px;
      color: #5A5E62;
      opacity: .9;
    }
    &__action {
      display: flex;
      align-items: center;
      border: 1px solid #EBEBEE;
      border-radius: 5px;
      overflow: hidden;
      font-size: 12px;
      color: #5A5E62;
      &-label {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 36px;
        width: 50px;
        border: none;
        background: #fff;
        cursor: pointer;
        color: #5A5E62;
      }
      &-count {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 36px;
        width: 50px;
        background: #F9F9FA;;
      }
    }
  }
  .reply-btn {
    display: inline-flex;
    align-items: center;
    background: #9C9EA1;
    border-radius: 5px;
    padding: 0 10px;
    height: 34px;
    color: #fff;
    gap: 10px;
    cursor: pointer;
    @include transition();
    svg {
      margin-top: -2px;
    }
    &:hover {
      background: #1ba098;
    }
  }
}
.review-reply {
  border-top: 1px solid #EBEBEE;
  padding-left: 60px;
  position: relative;
  padding-top: 40px;
  &__label {
    display: flex;
    align-items: center;
    gap: 15px;
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: #9C9EA1;
    margin-bottom: 40px;
  }
  .review {
    padding-bottom: 0;
    &::before {
      content: '';
      position: absolute;

      border-left: 3px solid #EBEBEE;
      height: calc(100% - 40px);
      left: 0;
      top: 40px;
    }
  }
}
.aside-rating {
  background: #F9F9FA;
  border: 1px solid #EBEBEE;
  border-radius: 5px;
  padding: 30px;
  margin-bottom: 25px;
}
.rating-filter {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #EBEBEE;
  &__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    @include response(992px) {
      margin-bottom: 0;
      &.active {
        margin-bottom: 25px;
        svg {
          transform: rotate(180deg);
        }
      }
    }
    svg {
      display: none;
      @include transition();
      @include response(992px) {
        display: block;
      }
    }
  }
  &__list {
    @include response(992px) {
      display: none;
    }
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    margin-bottom: 15px;
    cursor: pointer;
    &-left,
    &-right {
      width: 40px;
      flex-shrink: 0;
      font-weight: 600;
      font-size: 15px;
      color: #5A5E62;
      display: flex;
      align-items: center;
      gap: 10px;
    }
    &-progress {
      width: 100%;
      height: 5px;
      background: #EBEBEE;
      border-radius: 5px;
      position: relative;
      overflow: hidden;
      &-inner {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        background: #1ba098;
      }
    }
  }
}
.rating-box {
  width: 21px;
  height: 21px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border: 1px solid rgba(#5A5E62, .4);
  border-radius: 5px;
  &__overlay {
    position: absolute;
    left: -1px;
    top: -1px;
    right: -1px;
    bottom: -1px;
    border-radius: 5px;
    background: #1ba098;
    opacity: 0;
    @include transition();
  }
  svg {
    position: relative;
    z-index: 2;
  }
  input {
    width: 0;
    height: 0;
  }
  input:checked + .rating-box__overlay {
    opacity: 1;
  }
}
.go-to-review {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  width: 100%;
  background: #1ba098;
  color: #fff;
  font-weight: 500;
  font-size: 14px;
  height: 46px;
  border-radius: 5px;
  text-decoration: none;
}
.review-alert {
  background: #F9F9FA;
  border-radius: 5px;
  padding: 50px;
  &__title {
    margin-bottom: 15px;
  }
  &__desc {
    line-height: 175%;
    opacity: .9;
    max-width: 380px;
    margin-bottom: 10px;
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 15px;
    a {
      border-radius: 5px;
      height: 46px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-decoration: none;
      font-size: 14px;
      @include transition();
    }
    .login-btn {
      background: #0b1014;
      color: #fff;
      padding: 0 16px;
      width: 97px;
      &:hover {
        background: #1ba098;
      }
    }
    .register-btn {
      background: #fff;
      color: #0b1014;
      padding: 0 34px;
      &:hover {
        background: #1ba098;
        color: #fff;
      }
    }
  }
}
#more .h1 {
  margin-bottom: 60px;
}
.more-from-brand {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  .best-products__item {
    margin: 0;
  }
}
.review-form {
  background: #FFFFFF;
  border: 1px solid #EBEBEE;
  border-radius: 5px;
  padding: 40px;
  max-width: 730px;
  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 125%;
    margin-bottom: 10px;
  }
  &__desc {
    font-size: 15px;
    line-height: 155%;
    color: #5A5E62;
    opacity: 0.9;
    margin-bottom: 30px;
  }
  &-rate {
    display: flex;
    align-items: center;
    gap: 27px;
    margin-bottom: 25px;
    &__label {
      font-weight: 500;
      font-size: 14px;
      color: #5A5E62;
      opacity: 0.9;
    }
    &-stars {
      display: flex;
      padding: 0;
      border: none;
      flex-direction: row-reverse;
      gap: 4px;
      input {
        height: 0;
        width: 0;
      }
      path {
        fill: rgba(90, 94, 98, 0.4);
      }
    }
  }
}
.review-form-rate-stars > input:checked ~ label path, /* show gold star when clicked */
.review-form-rate-stars:not(:checked) > label:hover path, /* hover current star */
.review-form-rate-stars:not(:checked) > label:hover ~ label path { fill: #1ba098;  } /* hover previous stars in list */

.review-form-rate-stars > input:checked + label:hover path, /* hover current star when changing rating */
.review-form-rate-stars > input:checked ~ label:hover path,
.review-form-rate-stars > label:hover ~ input:checked ~ label path, /* lighten current selection */
.review-form-rate-stars > input:checked ~ label:hover ~ label path { fill: #1ba098;  }