.auth-page {
  display: flex;
  align-items: stretch;
  &__left {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    max-width: 810px;
    width: 100%;
    @include response(1023px) {
      display: none;
    }
  }
  &__right {
    width: 100%;
    padding: 100px 50px 100px 170px;
    @include response(1400px) {
      padding: 100px 100px;
    }
    @include response(1200px) {
      padding: 100px 50px;
    }
    @include response(1023px) {
      display: flex;
      justify-content: center;
      padding: 100px 20px;
    }
    @include response(767px) {
      padding: 30px 20px 50px;
    }
  }
  &__inner {
    width: 100%;
    @extend %flex-c;
    padding: 100px 20px 125px;
    @include response(767px) {
      padding: 30px 20px 50px;
    }
  }
}

.auth-form {
  max-width: 430px;
  width: 100%;
  background: #fff;
  border: 1px solid #ebebee;
  border-radius: 5px;
  padding: 50px;
  @include response(480px) {
    padding: 50px 20px;
  }
  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: #000;
    margin-bottom: 24px;
    @include response(480px) {
      font-size: 22px;
      text-align: center;
    }
  }
  &__alternative {
    position: relative;
    margin: 35px 0;
    font-weight: 500;
    font-size: 14px;
    color: #5a5e62;
    text-align: center;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 32%;
      height: 1px;
      background: #ebebee;
    }
    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 32%;
      height: 1px;
      background: #ebebee;
    }
    @include response(480px) {
      margin: 25px 0 20px;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin: 0 -2px 50px;
    @include response(480px) {
      margin: 0 -2px 40px;
    }
  }
  &__socials-button {
    @extend %flex-c;
    max-width: calc(33.3333% - 4px);
    width: 100%;
    flex-basis: calc(33.3333% - 4px);
    margin: 0 2px;
    height: 46px;
    background: linear-gradient(180deg, #fff 0%, #f9f9fa 100%);
    border: 1px solid #ebebee;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
    font-weight: 600;
    font-size: 12px;
    color: #5a5e62;
    svg {
      margin-right: 7px;
      transition: .3s ease-in-out;
    }
    @include hover() {
      background: #f9f9fa;
      svg {
        transform: scale(1.2);
      }
    }
  }
  &__bottom-text {
    text-align: center;
    font-weight: 500;
    font-size: 14px;
    color: #5a5e62;
    a {
      color: $green;
      text-decoration: none;
      @include hover() {
        text-decoration: underline;
      }
    }
  }
  &__row {
    @extend %flex-v;
    justify-content: space-between;
    margin: 25px 0;
    @include response(480px) {
      flex-direction: column;
      align-items: flex-start;
      margin: 25px 0 20px;
    }
  }
  &__password {
    font-weight: 500;
    font-size: 14px;
    color: $green;
    text-decoration: none;
    @include hover() {
      text-decoration: underline;
    }
    @include response(480px) {
      margin: 30px auto 0;
    }
  }
  &__description {
    font-size: 16px;
    line-height: 175%;
    color: #5a5e62;
    margin-bottom: 25px;
    span {
      font-weight: 600;
      color: $green;
    }
  }
  &--recover-password {
    .auth-form__bottom-text {
      margin-top: 40px;
    }
  }
  &--receive-email {
    .auth-form__bottom-text {
      margin-bottom: 30px;
    }
  }
  &--change-password {
    .form-block {
      margin-bottom: 20px;
      &:last-of-type {
        margin-bottom: 30px;
      }
    }
  }
  &--nickname {
    .auth-form__description {
      font-size: 13px;
    }
    .form-block {
      margin-bottom: 20px;
    }
  }
}