.footer {
  background: $black;
  &__main {
    padding: 100px 0 50px;
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include response(1200px) {
      padding: 100px 20px 50px;
    }
    @include response(991px) {
      flex-direction: column;
      .footer-col {
        max-width: 350px;
        width: 100%;
        margin: 0 auto 25px;
        &:nth-child(2) {
          order: -2;
        }
        &:nth-child(3) {
          order: -1;
        }
        &:nth-child(4) {
          .footer-subtitle {
            display: none;
          }
        }
      }
    }
    @include response(767px) {
      padding: 30px 30px 0;
      .footer-col {
        max-width: 100%;
      }
    }
  }
  &__bottom {
    position: relative;
    @extend %flex-v;
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
    padding: 50px 0;
    @include response(1200px) {
      padding: 50px 20px;
    }
    @include response(991px) {
      flex-direction: column;
      padding: 0 20px 35px;
    }
    @include response(767px) {
      padding: 0 30px 35px;
    }
  }
}

.footer-subtitle {
  position: relative;
  font-family: $Tfont;
  font-weight: 700;
  font-size: 19px;
  color: #fff;
  padding-bottom: 14px;
  margin-bottom: 30px;
  &::before {
    content: '';
    position: absolute;
    background: $brown;
    height: 4px;
    width: 40px;
    bottom: -4px;
    left: 0;
  }
  @include response(991px) {
    padding: 0;
    margin: 0;
    &::before {
      display: none;
    }
    &.footer-accardion__title {
      &::after {
        border-bottom: 1px solid #fff;
        border-right: 1px solid #fff;
        content: '';
        height: 7px;
        width: 7px;
        margin-top: -4px;
        pointer-events: none;
        position: absolute;
        right: 0;
        top: 50%;
        transform-origin: 66% 66%;
        transform: rotate(45deg);
        transition: all 0.15s ease-in-out;
      }
    }
    &.active {
      color: $brown;
      &::after {
        border-color: $brown;
        transform: rotate(-135deg);
      }
    }
  }
}

.footer-nav {
  &__list {
    @extend %list;
    li {
      font-size: 14px;
      line-height: 14px;
      margin-bottom: 25px;
      &:last-of-type {
        margin-bottom: 0;
      }
      a {
        color: #fff;
        text-decoration: none;
        transition: .3s ease-in-out;
        @include hover {
          color: $brown;
        }
      }
    }
  }
  @include response(991px) {
    display: none;
    padding: 30px 0 0;
  }
}

.footer-socials {
  @extend %list;
  @extend %flex-v;
  margin: 0 -15px;
  li {
    font-size: 14px;
    line-height: 14px;
    margin: 0 15px;
    a {
      @extend %flex-v;
      color: #fff;
      text-decoration: none;
      transition: .3s ease-in-out;
      span {
        @extend %flex-c;
        width: 18px;
        height: 18px;
        background: $brown;
        border-radius: 50%;
        margin-right: 10px;
        transition: .3s ease-in-out;
        svg {
          fill: $black;
          transition: .3s ease-in-out;
        }
      }
      @include hover() {
        color: $brown;
        span {
          background: #e6cbad;
        }
        svg {
          transform: scale(1.2);
        }
      }
    }
  }
  @include response(991px) {
    justify-content: center;
    li {
      font-size: 0;
      a {
        span {
          width: 35px;
          height: 35px;
          margin: 0;
          svg {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
  }
}

.podcast {
  margin-top: 30px;
  @include response(991px) {
    margin-top: 40px;
  }
  &__title {
    font-size: 14px;
    color: #fff;
    margin-bottom: 15px;
    @include response(991px) {
      text-align: center;
    }
  }
  &__box {
    @include response(991px) {
      text-align: center;
    }
  }
}

.footer-logo {
  margin-bottom: 25px;
  @include response(991px) {
    text-align: center;
    margin: 15px 0;
  }
}

.footer-options {
  &__item {
    @extend %flex-v;
    color: #fff;
    font-weight: 500;
    font-size: 14px;
    margin-bottom: 5px;
    span {
      font-weight: 600;
      font-size: 24px;
      margin-left: 10px;
    }
    @include response(991px) {
      justify-content: center;
    }
  }
}

.footer-email {
  color: #fff;
  font-size: 14px;
  text-decoration: none;
  span {
    color: $brown;
  }
  @include hover() {
    text-decoration: underline;
  }
  @include response(991px) {
    display: flex;
    justify-content: center;
    margin: 15px 0 0;
  }
}

.copyright {
  font-size: 15px;
  color: #fff;
  @include response(991px) {
    margin-bottom: 30px;
    color: rgba(255, 255, 255, 0.5);
  }
}

.top-button {
  position: absolute;
  left: 100%;
  margin-left: 120px;
  top: 50%;
  transform: translateY(-50%);
  @extend %flex-v;
  color: #fff;
  font-weight: 600;
  font-size: 12px;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  transition: .3s ease-in-out;
  span {
    flex-shrink: 0;
    @extend %flex-c;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: 1px solid #fff;
    margin-right: 15px;
    transition: .3s ease-in-out;
  }
  @include hover() {
    color: $brown;
    span {
      background: $brown;
      border-color: $brown;
    }
  }
  @include response(1600px) {
    margin-left: 0;
    left: auto;
    right: 0;
  }
  @include response(991px) {
    position: static;
    transform: none;
  }
}