.contact-main {
  max-width: 1160px;
  width: 100%;
  margin: 0 auto;
  padding: 100px 0 85px;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  @include response(1200px) {
    padding: 100px 20px 85px;
  }
  @include response(1023px) {
    flex-direction: column;
  }
  @include response(1023px) {
    padding: 50px 20px 65px;
  }
  @include response(767px) {
    padding: 30px 20px 50px;
  }
  &__content {
    max-width: 435px;
    width: 100%;
    flex-basis: 435px;
    @include response(1023px) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      max-width: 100%;
      flex-basis: auto;
      margin: 0 auto 50px;
    }
  }
  &__form {
    max-width: 600px;
    width: 100%;
    flex-basis: 600px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    padding: 35px 40px 86px;
    margin-left: 40px;
    .main-input {
      height: 46px;
    }
    @include response(1023px) {
      max-width: 100%;
      flex-basis: auto;
      margin: 0 auto;
    }
    @include response(767px) {
      padding: 40px 20px 50px;
      .main-input {
        height: 40px;
      }
    }
  }
  &__title {
    font-family: $Tfont;
    font-weight: 700;
    font-size: 54px;
    line-height: 125%;
    margin-bottom: 30px;
    @include response(767px) {
      font-size: 34px;
      margin-bottom: 25px;
    }
  }
  &__text {
    font-size: 18px;
    line-height: 175%;
    margin-bottom: 25px;
    @include response(767px) {
      font-size: 15px;
    }
  }
  &__subtitle {
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    margin-bottom: 20px;
    @include response(1023px) {
      order: 1;
    }
    @include response(767px) {
      font-size: 18px;
      margin-bottom: 15px;
    }
  }
  &__description {
    font-size: 16px;
    line-height: 175%;
    color: #5a5e62;
    margin-bottom: 25px;
    @include response(1023px) {
      order: 2;
    }
    @include response(767px) {
      margin-bottom: 20px;
    }
  }
  &__button {
    @extend %flex-c;
    max-width: 280px;
    width: 100%;
    height: 46px;
    background: $black;
    border: none;
    border-radius: 5px;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    transition: .3s ease-in-out;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: rgba(11, 16, 20, 0.7)
    }
    @include response(1023px) {
      order: 3;
    }
  }
  &__podcast {
    margin-top: 50px;
    @include response(1023px) {
      margin: 30px 0 40px;
    }
    @include response(580px) {
      width: 100%;
      img {
        width: 100%;
      }
    }
  }
  &__podcast-title {
    font-size: 16px;
    margin-bottom: 10px;
  }
  &__contacts {
    @extend %flex-v;
    justify-content: space-between;
    padding: 26px 0;
    border-top: 1px solid #ebebee;
    border-bottom: 1px solid #ebebee;
    margin-bottom: 20px;
    @include response(1200px) {
      flex-direction: column;
      align-items: flex-start;
    }
    @include response(1023px) {
      width: 100%;
      margin-bottom: 0;
    }
    @include response(767px) {
      padding-top: 0;
      border-top: none;
    }
  }
  &__email {
    font-weight: 500;
    font-size: 15px;
    color: $black;
    text-decoration: none;
    margin-right: 10px;
    span {
      color: $green;
    }
    @include hover() {
      span {
        text-decoration: underline;
      }
    }
    @include response(1200px) {
      margin: 0 0 20px;
    }
  }
  &__socials {
    @extend %list;
    @extend %flex-v;
    margin: 0 -10px;
    li {
      font-size: 14px;
      line-height: 14px;
      margin: 0 10px;
      a {
        @extend %flex-v;
        color: $black;
        text-decoration: none;
        transition: .3s ease-in-out;
        span {
          @extend %flex-c;
          width: 18px;
          height: 18px;
          background: $green;
          border-radius: 50%;
          margin-right: 10px;
          transition: .3s ease-in-out;
          svg {
            width: 10px;
            height: 10px;
            fill: #fff;
            transition: .3s ease-in-out;
          }
        }
        @include hover() {
          color: $green;
          svg {
            transform: scale(1.2);
          }
        }
      }
    }
    @include response(991px) {
      justify-content: center;
      li {
        a {
          span {
            width: 28px;
            height: 28px;
            svg {
              width: 16px;
              height: 16px;
            }
          }
        }
      }
    }
  }
  &__form-bottom {
    @extend %flex-v;
    justify-content: space-between;
    .submit-button {
      max-width: 230px;
      margin-left: 20px;
    }
    @include response(580px) {
      flex-direction: column;
      align-items: flex-start;
      .submit-button {
        margin: 30px 0 0;
      }
    }
  }
}

.contact-submit {
  padding: 100px 20px;
  @include response(767px) {
    padding: 30px 20px;
  }
  &__form {
    max-width: 600px;
    width: 100%;
    margin: 0 auto;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    padding: 50px 40px;
    .main-input {
      height: 46px;
      @include response(767px) {
        height: 40px;
      }
    }
    .form-block {
      margin-bottom: 25px;
    }
    .form-block__row {
      margin-bottom: 30px;
      .form-block {
        margin-bottom: 0;
      }
      @include response(767px) {
        margin-bottom: 0;
        .form-block {
          margin-bottom: 20px;
        }
      }
    }
    .checkbox-block input[type=checkbox] + span {
      &::before {
        top: 1px;
      }
      &::after {
        top: 1px;
      }
    }
    @include response(767px) {
      padding: 40px 20px 50px;
    }
  }
  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    margin-bottom: 10px;
    @include response(767px) {
      font-size: 22px;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 155%;
    color: rgba(90, 94, 98, 0.9);
    margin-bottom: 30px;
  }
}

.load-image {
  @extend %flex-v;
  margin-bottom: 40px;
  @include response(767px) {
    flex-direction: column;
    margin-bottom: 20px;
  }
  input[type=file] {
    display: none;
  }
  label {
    max-width: 180px;
    width: 100%;
    flex-basis: 180px;
    height: 120px;
    background: rgba(255, 255, 255, 0.8);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    cursor: pointer;
    transition: .3s ease-in-out;
    @include hover() {
      background: rgba(0, 0, 0, 0.075);
    }
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
      height: 54px;
      margin: 0 auto 20px;
    }
  }
  &__button {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    svg {
      margin-bottom: 20px;
    }
    @include response(767px) {
      flex-direction: row;
      svg {
        margin: 0 18px 0 0;
      }
    }
  }
  &__button-title {
    font-weight: 600;
    font-size: 12px;
    line-height: 12px;
    color: rgba(0, 0, 0, 0.5);
  }
  &__images {
    @extend %flex-v;
    flex-wrap: wrap;
    max-width: 324px;
    width: 100%;
    flex-basis: 324px;
    @include response(767px) {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__image {
    max-width: calc(33.33333% - 14px);
    width: 100%;
    flex-basis: calc(33.33333% - 14px);
    margin: 0 7px;
    height: 80px;
    overflow: hidden;
    background: #f9f9fa;
    border-radius: 5px;
    overflow: hidden;
    img {
      height: 100%;
      width: 100%;
    }
  }
}

.submit-mezcal-bottle {
  display: none;
}

.form-block {
  &__dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    max-width: 400px;
    width: 100%;
    background: #fff;
    border: 1px solid #ebebee;
    box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    z-index: 2;
    padding: 15px 15px 6px;
  }
  &__dropdown-item {
    @extend %flex-v;
    height: 45px;
    font-size: 16px;
    border-bottom: 1px solid #ebebee;
    cursor: pointer;
    transition: .3s ease-in-out;
    img {
      margin-right: 15px;
    }
    &:last-of-type {
      border: none;
    }
    @include hover() {
      background: #ebebee;
    }
  }
  &__dropdown-clear-title {
    @extend %flex-v;
    margin-bottom: 34px;
    font-size: 12px;
    line-height: 175%;
    color: #5a5e62;
    svg {
      margin-right: 10px;
    }
  }
  &__dropdown-clear {
    padding: 10px 0 20px;
  }
  &__dropdown-clear-text {
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: #5a5e62;
    margin-bottom: 15px;
  }
  &__dropdown-button {
    @extend %flex-c;
    background: $black;
    max-width: 152px;
    width: 100%;
    height: 35px;
    border-radius: 5px;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    border: none;
    cursor: pointer;
    text-decoration: none;
    transition: .3s ease-in-out;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: rgba(11, 16, 20, 0.75);
    }
  }
}
