.learning-heading {
  background: #f9f9fa;
  position: relative;
  padding-bottom: 40px;
  padding-top: 40px;
  margin-bottom: 80px;
  overflow: hidden;
  &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 40px;
    background-image: url("../data/menu-line.jpg");
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    max-width: 1300px;
    margin: 0 auto;
    @include response(992px) {
      align-items: flex-end;
    }
  }
  &__bg-left,
  &__bg-right {
    align-self: flex-end;
  }
  &__content {
    margin: 40px 30px 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include response(992px) {
      margin: 0 15px 25px;
    }
  }
  &__label {
    font-size: 14px;
    line-height: 125%;
    color: #5A5E62;
  }
  &__title {
    font-family: 'Playfair Display', sans-serif;
    font-weight: 700;
    font-size: 54px;
    line-height: 125%;
    margin-bottom: 20px;
    @include response(992px) {
      font-size: 32px;
    }
    @include response(767px) {
      font-size: 28px;
    }
  }
  &__info {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-bottom: 40px;
    &-item {
      display: flex;
      align-items: center;
      font-size: 12px;
      font-weight: 600;
      color: #0B1014;
      gap: 5px;
      line-height: 12px;
      span {
        font-weight: 400;
        color: #5A5E62;
        @include response(767px) {
          display: none;
        }
      }
    }
    @include response(767px) {
     margin-bottom: 25px;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    gap: 10px;
    &-label {
      font-weight: 500;
      font-size: 14px;
      line-height: 14px;
      color: #5A5E62;
    }
    .author__socials li {
      width: 32px;
      height: 32px;
    }
  }
  &__links {
    display: flex;
    align-items: center;
    gap: 12px;
    @include response(767px) {
      flex-wrap: wrap;
      justify-content: center;
      max-width: 205px;
      margin: 0 auto;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    padding: 0 16px;
    height: 50px;
    gap: 16px;
    font-family: 'Playfair Display', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #0B1014;
    background: #FFFFFF;
    border: 1px solid #EBEBEE;
    border-radius: 5px;
    text-decoration: none;
    @include transition();
    &:hover {
      background: #deb992;
      color: #fff;
    }
    @include response(767px) {
      justify-content: center;
      font-size: 14px;
      text-align: center;
      width: 100%;
      padding: 0 12px;
    }
  }
  @include response(992px) {
    margin-bottom: 30px;
    .learning-heading__bg-left,
    .learning-heading__bg-right {
      max-width: 100px;
    }
  }
}